import React from 'react';
import { Download } from 'lucide-react';

const DownloadButton = ({ treatyTitle, articles }) => {

  const generateCSVContent = () => {
    const headers = ['條文編號', '標題', '內容', '大學實踐範圍', 'SDGs 目標', '補充資料'];

    if (!articles || articles.length === 0) {
      console.warn("Articles data is empty.");
      return null;
    }

    const rows = articles.map(article => {
      const content = Array.isArray(article.content)
        ? article.content.map(item => item.text).join(' ')
        : (article.content || '').toString();

      const universityPractice = Array.isArray(article.universityPractice)
        ? article.universityPractice.join(' ')
        : (article.universityPractice || '').toString();

      const sdgs = Array.isArray(article.sdgs) && article.sdgs.length > 0
        ? article.sdgs.join('; ')
        : '無相關 SDGs';

      const resources = Array.isArray(article.resources) && article.resources.length > 0
        ? article.resources.map(resource => `${resource.title}: ${resource.content}`).join('; ')
        : '無補充資料';

      return [
        article.article,
        `第${article.article}條`,
        content,
        universityPractice,
        sdgs,
        resources
      ];
    });

    if (rows.length === 0) {
      console.warn("No rows generated from articles data.");
      return null;
    }

    let csvContent = headers.join(",") + "\n" 
      + rows.map(row => row.map(cell => `"${(cell || '').toString().replace(/"/g, '""')}"`).join(",")).join("\n");

    return csvContent;
  };

  const downloadCSV = () => {
    try {
      const csvContent = generateCSVContent();

      if (!csvContent) {
        alert("Unable to generate CSV content. Please check the data.");
        return;
      }

      const blob = new Blob([`\uFEFF${csvContent}`], { type: 'text/csv;charset=utf-8;' });
      const url = URL.createObjectURL(blob);
      const link = document.createElement("a");
      link.setAttribute("href", url);
      link.setAttribute("download", `${treatyTitle}.csv`);
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);

      console.log("CSV file generated and downloaded successfully.");

    } catch (error) {
      console.error("Error downloading CSV file:", error);
      alert("Error downloading CSV file. Please check the console for details.");
    }
  };

  return (
    <button
      onClick={downloadCSV}
      className="flex items-center px-4 py-2 bg-blue-500 text-white rounded-md transition duration-300 hover:bg-blue-600"
    >
      <Download size={16} className="mr-2" />
      下載條約內容
    </button>
  );
};

export default DownloadButton;
