import React, { useState, useCallback, useMemo } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ChevronLeft, ChevronRight, MessageCircle, AlertTriangle, Activity } from 'lucide-react';

const ImpactAnalysis = ({ assessment, updateAssessment }) => {
  const [currentRiskIndex, setCurrentRiskIndex] = useState(0);
  const [showAI, setShowAI] = useState(false);
  const [activeField, setActiveField] = useState('analysis');

  const currentRisk = assessment.identifiedRisks[currentRiskIndex];
  const currentAnalysis = assessment.impactAnalysis[currentRiskIndex] || {};

  const fields = useMemo(() => [
    { name: 'analysis', label: '影響分析', type: 'textarea' },
    { name: 'severity', label: '影響程度', type: 'select', options: [
      { value: '1', label: '1 - 輕微' },
      { value: '2', label: '2 - 中等' },
      { value: '3', label: '3 - 嚴重' },
      { value: '4', label: '4 - 非常嚴重' },
      { value: '5', label: '5 - 災難性' },
    ]},
    { name: 'scope', label: '影響範圍', type: 'select', options: [
      { value: '個人', label: '個人' },
      { value: '群體', label: '群體' },
      { value: '社區', label: '社區' },
      { value: '全社會', label: '全社會' },
    ]},
    { name: 'duration', label: '持續時間', type: 'select', options: [
      { value: '短期', label: '短期' },
      { value: '中期', label: '中期' },
      { value: '長期', label: '長期' },
    ]},
  ], []);

  const handleChange = useCallback((name, value) => {
    const updatedImpactAnalysis = [...assessment.impactAnalysis];
    updatedImpactAnalysis[currentRiskIndex] = {
      ...updatedImpactAnalysis[currentRiskIndex],
      riskId: currentRisk.description,
      [name]: value,
    };
    updateAssessment('impactAnalysis', updatedImpactAnalysis);
  }, [assessment.impactAnalysis, currentRisk, currentRiskIndex, updateAssessment]);

  const handleNavigation = useCallback((direction) => {
    setCurrentRiskIndex(prev => {
      if (direction === 'next' && prev < assessment.identifiedRisks.length - 1) {
        return prev + 1;
      } else if (direction === 'prev' && prev > 0) {
        return prev - 1;
      }
      return prev;
    });
    setActiveField('analysis');
  }, [assessment.identifiedRisks.length]);

  const handleAISuggest = useCallback((suggestion) => {
    handleChange('analysis', suggestion);
    setShowAI(false);
  }, [handleChange]);

  const renderField = useCallback((field) => {
    if (field.type === 'textarea') {
      return (
        <div className="relative">
          <textarea
            value={currentAnalysis[field.name] || ''}
            onChange={(e) => handleChange(field.name, e.target.value)}
            rows={6}
            className="w-full p-3 bg-gray-700 border border-gray-600 rounded-lg text-white resize-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition duration-200"
            placeholder={`描述${field.label}...`}
          />
          <button
            onClick={() => setShowAI(true)}
            className="absolute right-2 top-2 text-blue-400 hover:text-blue-300 transition duration-200"
            title="獲取 AI 建議"
          >
            <MessageCircle size={20} />
          </button>
        </div>
      );
    } else if (field.type === 'select') {
      return (
        <select
          value={currentAnalysis[field.name] || ''}
          onChange={(e) => handleChange(field.name, e.target.value)}
          className="w-full p-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:ring-2 focus:ring-blue-500 focus:border-transparent transition duration-200"
        >
          <option value="">選擇{field.label}</option>
          {field.options.map(option => (
            <option key={option.value} value={option.value}>{option.label}</option>
          ))}
        </select>
      );
    }
  }, [currentAnalysis, handleChange]);

  if (!currentRisk) {
    return (
      <div className="flex items-center justify-center h-64 text-gray-400">
        <AlertTriangle className="mr-2" />
        請先識別風險。
      </div>
    );
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="space-y-6 p-6 bg-gray-800 rounded-lg shadow-xl"
    >
      <h2 className="text-3xl font-bold mb-6 text-gray-100 flex items-center">
        <Activity className="mr-2 text-blue-500" />
        影響分析
      </h2>

      <div className="bg-gray-700 p-4 rounded-lg">
        <h3 className="text-xl font-semibold mb-4 text-gray-100 flex items-center">
          <AlertTriangle className="mr-2 text-yellow-500" />
          當前風險：{currentRisk.description}
        </h3>

        <div className="space-y-4">
          {fields.map((field) => (
            <motion.div
              key={field.name}
              initial={false}
              animate={{ height: activeField === field.name ? 'auto' : '40px' }}
              className="overflow-hidden"
            >
              <div
                className={`p-2 bg-gray-600 rounded-lg cursor-pointer ${
                  activeField === field.name ? 'bg-blue-500' : ''
                }`}
                onClick={() => setActiveField(field.name)}
              >
                <h4 className="text-lg font-medium text-white">{field.label}</h4>
                {activeField === field.name && (
                  <div className="mt-2">
                    {renderField(field)}
                  </div>
                )}
              </div>
            </motion.div>
          ))}
        </div>

        <div className="flex justify-between mt-6">
          <button
            onClick={() => handleNavigation('prev')}
            className={`px-4 py-2 flex items-center ${
              currentRiskIndex === 0 ? 'text-gray-500 cursor-not-allowed' : 'text-blue-400 hover:text-blue-300'
            } transition duration-200`}
            disabled={currentRiskIndex === 0}
          >
            <ChevronLeft size={20} className="mr-2" /> 上一個風險
          </button>
          <button
            onClick={() => handleNavigation('next')}
            className={`px-4 py-2 flex items-center ${
              currentRiskIndex === assessment.identifiedRisks.length - 1 ? 'text-gray-500 cursor-not-allowed' : 'text-blue-400 hover:text-blue-300'
            } transition duration-200`}
            disabled={currentRiskIndex === assessment.identifiedRisks.length - 1}
          >
            下一個風險 <ChevronRight size={20} className="ml-2" />
          </button>
        </div>
      </div>

      <AnimatePresence>
        {showAI && (
          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.95 }}
            transition={{ duration: 0.2 }}
            className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50"
          >
            <div className="bg-gray-800 p-6 rounded-lg shadow-xl max-w-md w-full">
              <h3 className="text-xl font-bold mb-4 text-white">AI 建議</h3>
              <p className="text-gray-300 mb-4">AI 正在生成影響分析建議...</p>
              <div className="flex justify-end space-x-2">
                <button
                  onClick={() => handleAISuggest("AI 生成的影響分析建議")}
                  className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition duration-200"
                >
                  採用建議
                </button>
                <button
                  onClick={() => setShowAI(false)}
                  className="px-4 py-2 bg-gray-600 text-white rounded hover:bg-gray-700 transition duration-200"
                >
                  關閉
                </button>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </motion.div>
  );
};

export default ImpactAnalysis;