import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Search, Filter, ArrowRight, Loader, Book, Scale, FileText } from 'lucide-react';
import { Link } from 'react-router-dom';
import Navbar from '../components/Navbar';

const LawListPage = () => {
  const [laws, setLaws] = useState({ studentUnion: [], university: [] });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('All');
  const [activeTab, setActiveTab] = useState('studentUnion');

  const categories = ['All', '組織', '選舉', '財務', '活動', '獎懲'];

  useEffect(() => {
    fetchLaws();
  }, []);

  const fetchLaws = async () => {
    setLoading(true);
    try {
      // 模擬 API 調用
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      const lawsData = {
        studentUnion: [
          { 
            id: 'su1', 
            title: '國立清華大學學生會組織章程', 
            category: '組織', 
            lastUpdated: '2024-03-15',
            level: '根本法',
            department: '學生會全會',
            description: '規定學生會的組織結構和運作方式。'
          },
          { 
            id: 'su2', 
            title: '國立清華大學學生會選舉罷免法', 
            category: '選舉', 
            lastUpdated: '2024-02-20',
            level: '法規',
            department: '學生會選舉委員會',
            description: '規範學生會各級幹部的選舉和罷免程序。'
          },
          { 
            id: 'su3', 
            title: '國立清華大學學生社團政策性活動補助辦法', 
            category: '財務', 
            lastUpdated: '2024-01-10',
            level: '行政規則',
            department: '人權及性別研究部',
            description: '訂定學生社團申請活動補助的標準和程序。'
          },
        ],
        university: [
          { 
            id: 'u1', 
            title: '國立清華大學學則', 
            category: '組織', 
            lastUpdated: '2024-02-28',
            level: '根本法',
            department: '教務處',
            description: '規範學生學籍、成績考核、畢業等學習相關事項。'
          },
          { 
            id: 'u2', 
            title: '國立清華大學學生獎懲辦法', 
            category: '獎懲', 
            lastUpdated: '2024-01-15',
            level: '法規',
            department: '學生事務處',
            description: '規定學生行為的獎勵與懲戒標準。'
          },
          { 
            id: 'u3', 
            title: '國立清華大學學生宿舍管理規則', 
            category: '活動', 
            lastUpdated: '2024-03-01',
            level: '行政規則',
            department: '學生住宿組',
            description: '規範學生宿舍的使用和管理。'
          },
        ]
      };

      setLaws(lawsData);
    } catch (error) {
      console.error("Error fetching laws:", error);
      setError("無法載入法規資料。請稍後再試。");
    } finally {
      setLoading(false);
    }
  };

  const filteredLaws = laws[activeTab].filter(law =>
    law.title.toLowerCase().includes(searchTerm.toLowerCase()) &&
    (selectedCategory === 'All' || law.category === selectedCategory)
  );

  const getLevelIcon = (level) => {
    switch (level) {
      case '根本法':
        return <Scale className="text-yellow-400" size={24} />;
      case '法規':
        return <Book className="text-blue-400" size={24} />;
      case '行政規則':
        return <FileText className="text-green-400" size={24} />;
      default:
        return <FileText className="text-gray-400" size={24} />;
    }
  };

  const LawCard = ({ law }) => (
    <motion.div
      layout
      initial={{ opacity: 0, y: 50 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -50 }}
      whileHover={{ scale: 1.03 }}
      className="bg-gradient-to-br from-gray-800 to-gray-900 rounded-lg shadow-lg overflow-hidden mb-4 p-4"
    >
      <div className="flex items-start justify-between">
        <div>
          <h3 className="text-xl font-semibold text-white mb-2">{law.title}</h3>
          <p className="text-gray-400 text-sm mb-2">最後更新: {law.lastUpdated}</p>
          <p className="text-gray-300 mb-2">{law.description}</p>
          <div className="flex items-center space-x-2 mb-2">
            <span className="px-2 py-1 bg-blue-600 text-white text-xs rounded-full">{law.category}</span>
            <span className="px-2 py-1 bg-purple-600 text-white text-xs rounded-full">{law.department}</span>
          </div>
        </div>
        <div className="flex flex-col items-center">
          {getLevelIcon(law.level)}
          <span className="text-xs text-gray-400 mt-1">{law.level}</span>
        </div>
      </div>
      <Link
        to={`/law/${law.id}`}
        className="inline-flex items-center mt-2 px-4 py-2 bg-gray-700 text-gray-200 rounded-full hover:bg-gray-600 transition-colors duration-300 group"
      >
        <span>查看完整法規</span>
        <ArrowRight size={16} className="ml-2 group-hover:translate-x-1 transition-transform duration-300" />
      </Link>
    </motion.div>
  );

  const renderContent = () => {
    if (loading) {
      return (
        <div className="flex items-center justify-center h-[calc(100vh-64px)]">
          <Loader className="animate-spin h-12 w-12 text-blue-400" />
        </div>
      );
    }

    if (error) {
      return (
        <div className="container mx-auto px-4 py-8">
          <div className="bg-red-900 border-l-4 border-red-500 text-red-100 p-4 rounded" role="alert">
            <p className="font-bold">錯誤</p>
            <p>{error}</p>
          </div>
        </div>
      );
    }

    return (
      <div className="container mx-auto px-4 py-8">
        <div className="flex flex-col md:flex-row justify-between items-center mb-8">
          <h1 className="text-4xl font-bold mb-4 md:mb-0 text-white">清華大學法規庫</h1>
          <div className="flex items-center space-x-4">
            <div className="relative">
              <input
                type="text"
                placeholder="搜尋法規..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full md:w-64 p-2 pl-10 pr-4 rounded-full bg-gray-700 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-400"
              />
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={18} />
            </div>
            <div className="relative inline-block text-left">
              <select
                value={selectedCategory}
                onChange={(e) => setSelectedCategory(e.target.value)}
                className="block appearance-none w-full bg-gray-700 border border-gray-600 text-white py-2 px-4 pr-8 rounded-full leading-tight focus:outline-none focus:bg-gray-600 focus:border-gray-500"
              >
                {categories.map(category => (
                  <option key={category} value={category}>{category}</option>
                ))}
              </select>
              <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-white">
                <Filter size={18} />
              </div>
            </div>
          </div>
        </div>

        <div className="flex justify-center mb-8">
          <button
            onClick={() => setActiveTab('studentUnion')}
            className={`px-6 py-2 rounded-l-full ${activeTab === 'studentUnion' ? 'bg-blue-600 text-white' : 'bg-gray-700 text-gray-300'}`}
          >
            學生會法規
          </button>
          <button
            onClick={() => setActiveTab('university')}
            className={`px-6 py-2 rounded-r-full ${activeTab === 'university' ? 'bg-blue-600 text-white' : 'bg-gray-700 text-gray-300'}`}
          >
            校方法規
          </button>
        </div>

        <motion.div layout className="space-y-4">
          <AnimatePresence>
            {filteredLaws.map(law => (
              <LawCard key={law.id} law={law} />
            ))}
          </AnimatePresence>
        </motion.div>

        {filteredLaws.length === 0 && (
          <div className="text-center py-12">
            <p className="text-2xl text-gray-400">沒有找到符合的法規</p>
            <p className="text-gray-500 mt-2">請嘗試調整搜尋條件或篩選器</p>
          </div>
        )}
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 to-gray-800 text-white">
      <Navbar />
      {renderContent()}
    </div>
  );
};

export default LawListPage;