import React, { useState, useCallback } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { MessageCircle, Plus, X, AlertTriangle } from 'lucide-react';

const RiskIdentification = ({ assessment, updateAssessment }) => {
  const [newRisk, setNewRisk] = useState({ description: '', category: '', likelihood: '', impact: '' });
  const [showAI, setShowAI] = useState(false);
  const [activeStep, setActiveStep] = useState(0);

  const riskCategories = ['勞工權益', '環境影響', '社區權利', '隱私權', '健康安全', '文化權利'];
  const likelihoodLevels = ['很低', '低', '中等', '高', '很高'];
  const impactLevels = ['輕微', '中等', '嚴重', '非常嚴重', '災難性'];

  const handleAddRisk = useCallback(() => {
    if (newRisk.description && newRisk.category && newRisk.likelihood && newRisk.impact) {
      updateAssessment('identifiedRisks', [...assessment.identifiedRisks, newRisk]);
      setNewRisk({ description: '', category: '', likelihood: '', impact: '' });
      setActiveStep(0);
    }
  }, [assessment.identifiedRisks, newRisk, updateAssessment]);

  const handleAISuggest = useCallback((suggestion) => {
    setNewRisk({ ...newRisk, description: suggestion });
    setShowAI(false);
  }, [newRisk]);

  const handleRemoveRisk = useCallback((index) => {
    const updatedRisks = assessment.identifiedRisks.filter((_, i) => i !== index);
    updateAssessment('identifiedRisks', updatedRisks);
  }, [assessment.identifiedRisks, updateAssessment]);

  const steps = [
    { title: '風險描述', field: 'description' },
    { title: '風險類別', field: 'category', options: riskCategories },
    { title: '發生可能性', field: 'likelihood', options: likelihoodLevels },
    { title: '影響程度', field: 'impact', options: impactLevels },
  ];

  const renderStepContent = () => {
    const step = steps[activeStep];
    if (step.options) {
      return (
        <div className="grid grid-cols-2 gap-2 mt-2">
          {step.options.map((option) => (
            <button
              key={option}
              onClick={() => {
                setNewRisk({ ...newRisk, [step.field]: option });
                setActiveStep((prev) => (prev < steps.length - 1 ? prev + 1 : prev));
              }}
              className={`p-2 rounded-lg transition-colors duration-200 ${
                newRisk[step.field] === option
                  ? 'bg-blue-500 text-white'
                  : 'bg-gray-700 hover:bg-gray-600 text-gray-200'
              }`}
            >
              {option}
            </button>
          ))}
        </div>
      );
    }
    return (
      <div className="relative">
        <textarea
          value={newRisk[step.field]}
          onChange={(e) => setNewRisk({ ...newRisk, [step.field]: e.target.value })}
          className="w-full p-3 bg-gray-700 border border-gray-600 rounded-lg text-white resize-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition duration-200"
          rows={3}
          placeholder={`請輸入${step.title}...`}
        />
        <button
          onClick={() => setShowAI(true)}
          className="absolute right-2 top-2 text-blue-400 hover:text-blue-300 transition duration-200"
          title="獲取 AI 建議"
        >
          <MessageCircle size={20} />
        </button>
      </div>
    );
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="space-y-6 p-6 bg-gray-800 rounded-lg shadow-xl"
    >
      <h2 className="text-3xl font-bold mb-6 text-gray-100 flex items-center">
        <AlertTriangle className="mr-2 text-yellow-500" />
        風險識別
      </h2>

      <div className="bg-gray-700 p-4 rounded-lg">
        <h3 className="text-xl font-semibold mb-4 text-gray-100">{steps[activeStep].title}</h3>
        {renderStepContent()}
        <div className="flex justify-between mt-4">
          <button
            onClick={() => setActiveStep((prev) => Math.max(0, prev - 1))}
            className="px-4 py-2 bg-gray-600 text-white rounded hover:bg-gray-500 transition duration-200"
            disabled={activeStep === 0}
          >
            上一步
          </button>
          <button
            onClick={() => {
              if (activeStep < steps.length - 1) {
                setActiveStep((prev) => prev + 1);
              } else {
                handleAddRisk();
              }
            }}
            className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition duration-200"
          >
            {activeStep === steps.length - 1 ? '添加風險' : '下一步'}
          </button>
        </div>
      </div>

      <div className="mt-6">
        <h3 className="text-xl font-semibold mb-4 text-gray-100">已識別風險</h3>
        <ul className="space-y-2">
          {assessment.identifiedRisks.map((risk, index) => (
            <motion.li
              key={index}
              initial={{ opacity: 0, x: -20 }}
              animate={{ opacity: 1, x: 0 }}
              exit={{ opacity: 0, x: 20 }}
              transition={{ duration: 0.3 }}
              className="bg-gray-700 p-4 rounded-lg flex justify-between items-start"
            >
              <div>
                <p className="font-semibold text-white">{risk.description}</p>
                <p className="text-sm text-gray-400">
                  類別: {risk.category} | 可能性: {risk.likelihood} | 影響: {risk.impact}
                </p>
              </div>
              <button
                onClick={() => handleRemoveRisk(index)}
                className="text-red-400 hover:text-red-300 transition duration-200"
              >
                <X size={20} />
              </button>
            </motion.li>
          ))}
        </ul>
      </div>

      <AnimatePresence>
        {showAI && (
          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.95 }}
            transition={{ duration: 0.2 }}
            className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50"
          >
            <div className="bg-gray-800 p-6 rounded-lg shadow-xl max-w-md w-full">
              <h3 className="text-xl font-bold mb-4 text-white">AI 建議</h3>
              <p className="text-gray-300 mb-4">AI 正在生成風險描述建議...</p>
              <div className="flex justify-end space-x-2">
                <button
                  onClick={() => handleAISuggest("AI 生成的風險描述建議")}
                  className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition duration-200"
                >
                  採用建議
                </button>
                <button
                  onClick={() => setShowAI(false)}
                  className="px-4 py-2 bg-gray-600 text-white rounded hover:bg-gray-700 transition duration-200"
                >
                  關閉
                </button>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </motion.div>
  );
};

export default RiskIdentification;