import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Search, Filter, Loader, X } from 'lucide-react';
import Navbar from '../components/Navbar';
import TreatyCard from '../components/TreatyCard';
import ComparisonModal from '../components/ComparisonModal';

const TreatyListPage = () => {
  const [treaties, setTreaties] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('All');
  const [showFilters, setShowFilters] = useState(false);
  const [selectedTreaties, setSelectedTreaties] = useState([]);
  const [showComparison, setShowComparison] = useState(false);

  const categories = ['All', 'Human Rights', 'Environment', 'Trade', 'Diplomacy'];

  useEffect(() => {
    fetchTreaties();
  }, []);

  const fetchTreaties = async () => {
    setLoading(true);
    try {
      const response = await fetch('http://localhost:3000/api/treaties');
      if (!response.ok) throw new Error('Failed to fetch treaties');
      const data = await response.json();
      setTreaties(data);
    } catch (error) {
      console.error("Error fetching treaties:", error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  };

  const filteredTreaties = treaties.filter(treaty =>
    treaty.title.toLowerCase().includes(searchTerm.toLowerCase()) &&
    (selectedCategory === 'All' || treaty.category === selectedCategory)
  );

  const handleTreatySelection = (treatyId) => {
    setSelectedTreaties(prev => {
      if (prev.includes(treatyId)) {
        return prev.filter(id => id !== treatyId);
      } else if (prev.length < 3) {
        return [...prev, treatyId];
      }
      return prev;
    });
  };

  const renderContent = () => {
    if (loading) {
      return (
        <div className="flex items-center justify-center h-[calc(100vh-64px)]">
          <Loader className="animate-spin h-12 w-12 text-blue-400" />
        </div>
      );
    }

    if (error) {
      return (
        <div className="container mx-auto px-4 py-8">
          <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 rounded" role="alert">
            <p className="font-bold">Error</p>
            <p>{error}</p>
          </div>
        </div>
      );
    }

    return (
      <div className="container mx-auto px-4 py-8">
        <div className="flex flex-col md:flex-row justify-between items-center mb-8">
          <h1 className="text-4xl font-bold mb-4 md:mb-0">國際條約庫</h1>
          <div className="flex items-center space-x-4">
            <div className="relative">
              <input
                type="text"
                placeholder="搜尋條約..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full md:w-64 p-2 pl-10 pr-4 rounded-full bg-gray-700 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-400"
              />
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={18} />
            </div>
            <button
              onClick={() => setShowFilters(!showFilters)}
              className="bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-4 rounded-full transition duration-300 flex items-center"
            >
              <Filter size={18} className="mr-2" />
              篩選
            </button>
          </div>
        </div>

        {showFilters && (
          <div className="mb-8 p-4 bg-gray-800 rounded-lg shadow-lg">
            <h2 className="text-xl font-semibold mb-4">類別篩選</h2>
            <div className="flex flex-wrap gap-2">
              {categories.map(category => (
                <button
                  key={category}
                  onClick={() => setSelectedCategory(category)}
                  className={`px-4 py-2 rounded-full transition duration-300 ${
                    selectedCategory === category
                      ? 'bg-blue-500 text-white'
                      : 'bg-gray-700 text-gray-300 hover:bg-gray-600'
                  }`}
                >
                  {category}
                </button>
              ))}
            </div>
          </div>
        )}

        {selectedTreaties.length > 0 && (
          <div className="mb-8 p-4 bg-blue-900 rounded-lg shadow-lg">
            <div className="flex justify-between items-center">
              <span className="text-lg font-semibold">已選擇 {selectedTreaties.length} 項條約</span>
              <button
                onClick={() => setShowComparison(true)}
                className="bg-green-500 hover:bg-green-600 text-white font-bold py-2 px-4 rounded-full transition duration-300 flex items-center"
              >
                比較所選條約
              </button>
            </div>
          </div>
        )}

        {filteredTreaties.length === 0 ? (
          <div className="text-center py-12">
            <p className="text-2xl text-gray-400">沒有找到符合的條約</p>
            <p className="text-gray-500 mt-2">請嘗試調整搜尋條件或篩選器</p>
          </div>
        ) : (
          <motion.div layout className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
            <AnimatePresence>
              {filteredTreaties.map(treaty => (
                <TreatyCard
                  key={treaty._id}
                  treaty={treaty}
                  handleTreatySelection={handleTreatySelection}
                  selectedTreaties={selectedTreaties}
                />
              ))}
            </AnimatePresence>
          </motion.div>
        )}
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900 text-white">
      <Navbar />
      {renderContent()}
      <ComparisonModal 
        showComparison={showComparison} 
        setShowComparison={setShowComparison}
        selectedTreaties={selectedTreaties.map(id => treaties.find(t => t._id === id))}
      />
    </div>
  );
};

export default TreatyListPage;