import React, { useState, useCallback, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ChevronRight, ChevronLeft, Check, MessageCircle } from 'lucide-react';
import Navbar from '../components/Navbar';
import LoadingSpinner from '../components/LoadingSpinner';
import ErrorMessage from '../components/ErrorMessage';
import ContextAnalysis from '../components/impact/ContextAnalysis';
import RiskIdentification from '../components/impact/RiskIdentification';
import ImpactAnalysis from '../components/impact/ImpactAnalysis';
import MitigationMeasures from '../components/impact/MitigationMeasures';
import MonitoringPlan from '../components/impact/MonitoringPlan';
import OverallAssessment from '../components/impact/OverallAssessment';
import AIAssistant from '../components/impact/AIAssistant';
import Button from '../components/impact/Button';

const HumanRightsImpactAssessment = () => {
  const [currentStep, setCurrentStep] = useState(0);
  const [assessment, setAssessment] = useState({
    context: {
      projectDescription: '',
      stakeholders: [],
      vulnerableGroups: [],
    },
    identifiedRisks: [],
    impactAnalysis: [],
    mitigationMeasures: [],
    monitoringPlan: '',
    overallImpact: '',
    overallJustification: '',
    keySummary: '',
    recommendedActions: '',
  });
  const [showGlobalAI, setShowGlobalAI] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  const steps = [
    { title: '背景分析', component: ContextAnalysis },
    { title: '風險識別', component: RiskIdentification },
    { title: '影響分析', component: ImpactAnalysis },
    { title: '緩解措施', component: MitigationMeasures },
    { title: '監測計劃', component: MonitoringPlan },
    { title: '總體評估', component: OverallAssessment },
  ];

  useEffect(() => {
    // Simulating data loading
    setTimeout(() => {
      setIsLoading(false);
    }, 1000);
  }, []);

  const handleNext = useCallback(() => {
    if (currentStep < steps.length - 1) {
      setCurrentStep(currentStep + 1);
    }
  }, [currentStep, steps.length]);

  const handlePrevious = useCallback(() => {
    if (currentStep > 0) {
      setCurrentStep(currentStep - 1);
    }
  }, [currentStep]);

  const handleUpdateAssessment = useCallback((key, value) => {
    setAssessment(prev => ({ ...prev, [key]: value }));
  }, []);

  const handleSubmit = useCallback(() => {
    console.log("提交評估結果:", assessment);
    // Add logic to send results to server or generate report
  }, [assessment]);

  const handleGlobalAISuggest = useCallback((suggestion) => {
    console.log("全局 AI 建議:", suggestion);
    setShowGlobalAI(false);
  }, []);

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gray-900 text-white">
        <Navbar />
        <div className="container mx-auto px-4 py-8">
          <LoadingSpinner />
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-900 text-white">
        <Navbar />
        <div className="container mx-auto px-4 py-8">
          <ErrorMessage message={error} />
        </div>
      </div>
    );
  }

  const CurrentStepComponent = steps[currentStep].component;

  return (
    <div className="min-h-screen bg-gray-900 text-white flex flex-col">
      <Navbar />
      <div className="flex-grow container mx-auto px-4 py-8">
        <motion.h1
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="text-4xl font-bold mb-8 text-center"
        >
          人權影響評估系統
        </motion.h1>
        <div className="max-w-4xl mx-auto bg-gray-800 rounded-lg shadow-lg p-6">
          <div className="flex mb-6 overflow-x-auto">
            {steps.map((step, index) => (
              <div
                key={index}
                className={`flex-shrink-0 text-center px-4 py-2 rounded-full mr-2 transition duration-300 ${
                  index === currentStep ? 'bg-blue-600 text-white' : 'bg-gray-700 text-gray-300'
                }`}
              >
                {step.title}
              </div>
            ))}
          </div>
          <AnimatePresence mode="wait">
            <motion.div
              key={currentStep}
              initial={{ opacity: 0, x: 20 }}
              animate={{ opacity: 1, x: 0 }}
              exit={{ opacity: 0, x: -20 }}
              transition={{ duration: 0.3 }}
              className="mb-6"
            >
              <CurrentStepComponent
                assessment={assessment}
                updateAssessment={handleUpdateAssessment}
              />
            </motion.div>
          </AnimatePresence>
          <div className="flex justify-between mt-8">
            <Button onClick={handlePrevious} disabled={currentStep === 0}>
              <ChevronLeft className="mr-2" />
              上一步
            </Button>
            {currentStep === steps.length - 1 ? (
              <Button onClick={handleSubmit} className="bg-green-500 hover:bg-green-600 transition duration-300">
                <Check className="mr-2" />
                提交評估
              </Button>
            ) : (
              <Button onClick={handleNext}>
                下一步
                <ChevronRight className="ml-2" />
              </Button>
            )}
          </div>
        </div>
      </div>
      <button
        onClick={() => setShowGlobalAI(true)}
        className="fixed bottom-4 right-4 bg-blue-500 hover:bg-blue-600 text-white p-2 rounded-full shadow-lg transition duration-300"
        title="全局 AI 助手"
      >
        <MessageCircle size={24} />
      </button>
      {showGlobalAI && (
        <AIAssistant onSuggest={handleGlobalAISuggest} onClose={() => setShowGlobalAI(false)} />
      )}
    </div>
  );
};

export default HumanRightsImpactAssessment;