import React from 'react';
import { Clock, TrendingUp } from 'lucide-react';

const TabContent = ({ activeTab, recentUpdates, trendingTopics }) => {
  if (activeTab === 'recent') {
    return (
      <ul className="space-y-3">
        {recentUpdates.map((update, index) => (
          <li key={index} className="flex justify-between items-center p-2 hover:bg-gray-700 rounded transition duration-300">
            <span className="text-blue-400 hover:text-blue-300 cursor-pointer flex items-center">
              <Clock size={16} className="mr-2" />
              {update.title}
            </span>
            <span className="text-gray-400 text-sm">{update.date}</span>
          </li>
        ))}
      </ul>
    );
  } else {
    return (
      <ul className="space-y-3">
        {trendingTopics.map((topic, index) => (
          <li key={index} className="flex justify-between items-center p-2 hover:bg-gray-700 rounded transition duration-300">
            <span className="text-blue-400 hover:text-blue-300 cursor-pointer flex items-center">
              <TrendingUp size={16} className="mr-2" />
              {topic.title}
            </span>
            <span className="text-gray-400 text-sm">{topic.count} 次檢索</span>
          </li>
        ))}
      </ul>
    );
  }
};

export default TabContent;
