import React, { useState, useEffect } from 'react';
import Navbar from '../components/Navbar';
import FeatureCard from '../components/FeatureCard';
import SearchBar from '../components/SearchBar';
import { motion } from 'framer-motion';
import { mockSuggestions, mockResults, features, recentUpdates, trendingTopics, communityHighlights } from '../data/mockData';
import TabContent from '../components/TabContent';
import CommunityHighlights from '../components/CommunityHighlights';
import NewcomerGuide from '../components/NewcomerGuide';

const HomePage = () => {
  const [searchResults, setSearchResults] = useState([]);
  const [showResults, setShowResults] = useState(false);
  const [activeTab, setActiveTab] = useState('recent');

  useEffect(() => {
    // 這裡原來的 isVisible 變量已經刪除
  }, []);

  const handleSearchResults = (results) => {
    setSearchResults(results);
    setShowResults(true);
  };

  const handleHideResults = () => {
    setShowResults(false);
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-900 to-gray-800 text-white">
      <Navbar />

      <main className="container mx-auto mt-12 p-4">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="text-center mb-12"
        >
          <h2 className="text-5xl font-bold mb-4">探索全球人權公約</h2>
          <p className="text-xl text-gray-300">提升您對人權的認識，推動社會正義</p>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.2 }}
        >
          <SearchBar 
            mockSuggestions={mockSuggestions} 
            mockResults={mockResults}
            onResults={handleSearchResults}
            onHideResults={handleHideResults}
          />

          {showResults && (
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              className="mt-6 bg-gray-800 rounded-lg p-6 shadow-lg"
            >
              <h3 className="text-2xl font-bold mb-4">搜尋結果</h3>
              {searchResults.map((result, index) => (
                <div key={index} className="mb-4 last:mb-0 p-4 bg-gray-700 rounded-lg hover:bg-gray-600 transition duration-300">
                  <h4 className="text-lg font-semibold text-blue-400">{result.title}</h4>
                  <p className="text-gray-300 mt-2">{result.content}</p>
                </div>
              ))}
            </motion.div>
          )}
        </motion.div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8 mt-16">
          {features.map((item, index) => (
            <FeatureCard
              key={index}
              icon={item.icon}
              title={item.title}
              description={item.description}
              color={item.color}
              delay={index * 0.1}
            />
          ))}
        </div>

        <div className="mt-16 grid grid-cols-1 md:grid-cols-2 gap-8">
          <motion.div
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
            className="bg-gray-800 rounded-lg p-6 shadow-lg"
          >
            <div className="flex justify-between mb-6">
              <button
                className={`px-4 py-2 rounded-full transition duration-300 ${activeTab === 'recent' ? 'bg-blue-600 text-white' : 'bg-gray-700 text-gray-300 hover:bg-gray-600'}`}
                onClick={() => setActiveTab('recent')}
              >
                最近更新
              </button>
              <button
                className={`px-4 py-2 rounded-full transition duration-300 ${activeTab === 'trending' ? 'bg-blue-600 text-white' : 'bg-gray-700 text-gray-300 hover:bg-gray-600'}`}
                onClick={() => setActiveTab('trending')}
              >
                熱門主題
              </button>
            </div>
            <TabContent activeTab={activeTab} recentUpdates={recentUpdates} trendingTopics={trendingTopics} />
          </motion.div>

          <CommunityHighlights communityHighlights={communityHighlights} />
        </div>

        <NewcomerGuide />
      </main>
    </div>
  );
};

export default HomePage;
