import React, { useState, useEffect, useRef } from 'react';
import { Search } from 'lucide-react';

const SearchBar = ({ mockSuggestions = [], mockResults, onResults, onHideResults }) => {
  const [searchQuery, setSearchQuery] = useState('');
  const [suggestions, setSuggestions] = useState([]);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const searchRef = useRef(null);

  useEffect(() => {
    if (searchQuery.length > 0) {
      const filtered = mockSuggestions.filter(item => 
        item.toLowerCase().includes(searchQuery.toLowerCase())
      );
      setSuggestions(filtered);
      setShowSuggestions(true);
      if (onHideResults) {
        onHideResults();  // 確保 onHideResults 存在後再調用
      }
    } else {
      setShowSuggestions(false);
    }
  }, [searchQuery, mockSuggestions, onHideResults]);

  const handleClickOutside = (event) => {
    if (searchRef.current && !searchRef.current.contains(event.target) && searchQuery.trim() === '') {
      setShowSuggestions(false);
    }
  };

  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [searchQuery]);

  const handleSearch = () => {
    setShowSuggestions(false);
    onResults(mockResults);  // 顯示新結果
  };

  const handleSuggestionClick = (suggestion) => {
    setSearchQuery(suggestion);
    setShowSuggestions(false);
    handleSearch();
  };

  return (
    <div className="w-full max-w-2xl mx-auto mb-12" ref={searchRef}>
      <div className="relative">
        <div className="flex">
          <input
            type="text"
            placeholder="輸入關鍵詞搜尋公約或案例"
            className="flex-grow bg-gray-700 text-white border border-gray-600 rounded-l-md p-3 focus:outline-none focus:ring-2 focus:ring-blue-400"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            onFocus={() => setShowSuggestions(true)}
            onBlur={() => setTimeout(() => setShowSuggestions(false), 200)}  // 添加延遲以確保搜尋結果顯示
          />
          <button 
            className="bg-blue-500 hover:bg-blue-600 text-white p-3 rounded-r-md transition duration-300"
            onClick={(e) => {
              e.stopPropagation();  // 阻止事件冒泡
              handleSearch();
            }}
          >
            <Search size={24} />
          </button>
        </div>
        
        {showSuggestions && suggestions.length > 0 && (
          <div className="absolute z-10 w-full bg-gray-800 border border-gray-700 rounded-md mt-1 shadow-lg">
            {suggestions.map((suggestion, index) => (
              <div 
                key={index} 
                className="p-2 hover:bg-gray-700 cursor-pointer"
                onClick={() => handleSuggestionClick(suggestion)}
              >
                {suggestion}
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default SearchBar;
