import React, { useState, useCallback } from 'react';
import { motion } from 'framer-motion';
import AIAssistant from './AIAssistant';
import Input from './Input';
import Select from './Select';
import Button from './Button';
import { MessageCircle } from 'lucide-react';

const MitigationMeasures = ({ assessment, updateAssessment }) => {
  const [newMeasure, setNewMeasure] = useState({ riskId: '', description: '', responsibleParty: '', timeline: '' });
  const [showAI, setShowAI] = useState(false);

  const handleAddMeasure = useCallback(() => {
    if (newMeasure.riskId && newMeasure.description) {
      updateAssessment('mitigationMeasures', [...assessment.mitigationMeasures, newMeasure]);
      setNewMeasure({ riskId: '', description: '', responsibleParty: '', timeline: '' });
    }
  }, [assessment.mitigationMeasures, newMeasure, updateAssessment]);

  const handleAISuggest = useCallback((suggestion) => {
    setNewMeasure({ ...newMeasure, description: suggestion });
    setShowAI(false);
  }, [newMeasure]);

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <h2 className="text-2xl font-bold mb-4">緩解措施</h2>
      <Select
        label="相關風險"
        value={newMeasure.riskId}
        onChange={(e) => setNewMeasure({ ...newMeasure, riskId: e.target.value })}
        options={[
          { value: '', label: '選擇相關風險' },
          ...assessment.identifiedRisks.map((risk) => ({
            value: risk.description,
            label: risk.description,
          })),
        ]}
      />
      <div className="mb-4 relative">
        <label className="block text-sm font-medium text-gray-200 mb-1">緩解措施描述</label>
        <textarea
          value={newMeasure.description}
          onChange={(e) => setNewMeasure({ ...newMeasure, description: e.target.value })}
          className="w-full p-2 bg-gray-700 border border-gray-600 rounded text-white pr-10 transition duration-300 focus:border-blue-500 focus:ring focus:ring-blue-200 focus:ring-opacity-50"
          rows={4}
          placeholder="描述緩解措施..."
        />
        <button
          onClick={() => setShowAI(true)}
          className="absolute right-2 top-8 text-blue-400 hover:text-blue-300 transition duration-300"
          title="獲取 AI 建議"
        >
          <MessageCircle size={20} />
        </button>
      </div>
      <Input
        label="負責方"
        value={newMeasure.responsibleParty}
        onChange={(e) => setNewMeasure({ ...newMeasure, responsibleParty: e.target.value })}
      />
      <Input
        label="實施時間表"
        value={newMeasure.timeline}
        onChange={(e) => setNewMeasure({ ...newMeasure, timeline: e.target.value })}
      />
      <Button onClick={handleAddMeasure} className="mt-2">添加緩解措施</Button>
      <ul className="list-disc pl-5 mt-4">
        {assessment.mitigationMeasures.map((measure, index) => (
          <li key={index} className="mb-4">
            <strong>{measure.riskId}:</strong>
            <p className="ml-4">{measure.description}</p>
            <p className="text-sm text-gray-400 ml-4">
              負責方: {measure.responsibleParty}<br />
              時間表: {measure.timeline}
            </p>
          </li>
        ))}
      </ul>
      {showAI && (
        <AIAssistant onSuggest={handleAISuggest} onClose={() => setShowAI(false)} />
      )}
    </motion.div>
  );
};

export default MitigationMeasures;
