import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Share2, Bookmark, BookmarkCheck, School, Gavel, AlertCircle, Lightbulb, Globe, BookOpen } from 'lucide-react';
import { Link } from 'react-router-dom';

const ArticleContent = ({ article, bookmarks, toggleBookmark, handleShare, handleFeedback }) => {
  const isBookmarked = bookmarks.includes(article.article);
  const [sdgDetails, setSdgDetails] = useState({});

  useEffect(() => {
    const fetchSdgDetails = async () => {
      const sdgDetailMap = {};
      for (const goalNumber of article.sdgs || []) {
        try {
          const response = await fetch(`http://localhost:3000/api/sdgs/${goalNumber}`);
          if (response.ok) {
            const data = await response.json();
            sdgDetailMap[goalNumber] = data;
          }
        } catch (error) {
          console.error(`Error fetching SDG ${goalNumber} details:`, error);
        }
      }
      setSdgDetails(sdgDetailMap);
    };

    fetchSdgDetails();
  }, [article.sdgs]);

  const SdgBadge = ({ goalNumber }) => (
    <motion.div
      className="flex items-center p-3 bg-gradient-to-r from-blue-500 to-green-500 rounded-lg shadow-md m-2"
      whileHover={{ scale: 1.05 }}
      whileTap={{ scale: 0.95 }}
    >
      <Link to={`/sdg/${goalNumber}`} className="flex items-center">
        <Globe size={24} className="mr-2" />
        <div>
          <div className="font-bold">SDG {goalNumber}</div>
          <div className="text-sm">{sdgDetails[goalNumber]?.title || 'Loading...'}</div>
        </div>
      </Link>
    </motion.div>
  );

  if (!article) {
    return <div className="text-center py-10">載入中...</div>;
  }

  // 生成唯一的標題
  const uniqueTitle = `第 ${article.article} 條 - ${article.title}`;

  console.log('Article Content:', article.content);

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="bg-gray-900 rounded-xl shadow-2xl overflow-hidden"
    >
      <div className="bg-gradient-to-r from-blue-600 to-purple-600 p-6">
        <div className="flex justify-between items-center">
          <h2 className="text-3xl font-bold text-white">{uniqueTitle}</h2>
          <div className="flex space-x-2">
            <motion.button
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.9 }}
              onClick={() => handleShare(article.article)}
              className="p-2 bg-white bg-opacity-20 rounded-full"
            >
              <Share2 size={20} className="text-white" />
            </motion.button>
            <motion.button
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.9 }}
              onClick={() => toggleBookmark(article.article)}
              className={`p-2 rounded-full ${isBookmarked ? 'bg-yellow-400' : 'bg-white bg-opacity-20'}`}
            >
              {isBookmarked ? <BookmarkCheck size={20} className="text-gray-800" /> : <Bookmark size={20} className="text-white" />}
            </motion.button>
          </div>
        </div>
      </div>

      <div className="p-6 space-y-6">
{article.content && Array.isArray(article.content) && article.content.length > 0 ? (
  <div className="bg-gray-800 p-4 rounded-lg">
    <h3 className="text-xl font-semibold text-white mb-3">內容</h3>
    <p className="text-gray-300">
      {article.content[0].text}
    </p>
  </div>
) : (
  <div className="bg-gray-800 p-4 rounded-lg">
    <h3 className="text-xl font-semibold text-white mb-3">內容</h3>
    <p className="text-gray-300">內容不可用</p>
  </div>
)}


        {article.universityPractice && (
          <div className="bg-gray-800 p-4 rounded-lg">
            <h3 className="text-xl font-semibold text-white mb-3 flex items-center">
              <School className="mr-2" /> 大學實踐範圍
            </h3>
            <p className="text-gray-300">{article.universityPractice}</p>
          </div>
        )}

        {article.regulations && article.regulations.length > 0 && (
          <div className="bg-gray-800 p-4 rounded-lg">
            <h3 className="text-xl font-semibold text-white mb-3 flex items-center">
              <Gavel className="mr-2" /> 相關法規
            </h3>
            <ul className="list-disc list-inside text-gray-300">
              {article.regulations.map((reg, index) => (
                <li key={index}>{reg}</li>
              ))}
            </ul>
          </div>
        )}

        {article.sdgs && article.sdgs.length > 0 && (
          <div className="bg-gray-800 p-4 rounded-lg">
            <h3 className="text-xl font-semibold text-white mb-3 flex items-center">
              <Globe className="mr-2" /> SDGs 目標
            </h3>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {article.sdgs.map((goalNumber) => (
                <SdgBadge key={goalNumber} goalNumber={goalNumber} />
              ))}
            </div>
          </div>
        )}

        {article.resources && article.resources.length > 0 && (
          <div className="bg-gray-800 p-4 rounded-lg">
            <h3 className="text-xl font-semibold text-white mb-3 flex items-center">
              <BookOpen className="mr-2" /> 相關資源
            </h3>
            {article.resources.map((resource, index) => (
              <div key={index} className="mb-4 last:mb-0">
                <h4 className="text-lg font-semibold text-blue-300">{resource.title}</h4>
                <p className="text-gray-300 mt-1">{resource.content}</p>
              </div>
            ))}
          </div>
        )}

        <div className="flex space-x-4 mt-6">
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            onClick={() => handleFeedback('error', uniqueTitle)}
            className="flex-1 flex items-center justify-center px-4 py-2 bg-red-500 text-white rounded-md"
          >
            <AlertCircle size={20} className="mr-2" />
            報錯
          </motion.button>
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            onClick={() => handleFeedback('suggestion', uniqueTitle)}
            className="flex-1 flex items-center justify-center px-4 py-2 bg-yellow-500 text-white rounded-md"
          >
            <Lightbulb size={20} className="mr-2" />
            提供建議
          </motion.button>
        </div>
      </div>
    </motion.div>
  );
};

export default ArticleContent;
