import React, { useState, useEffect, useRef } from 'react';
import { useParams, useLocation } from 'react-router-dom';
import Navbar from '../components/Navbar';
import LoadingSpinner from '../components/LoadingSpinner';
import ErrorMessage from '../components/ErrorMessage';
import SearchBar from '../components/SearchBar';
import ArticleContent from '../components/ArticleContent';
import FeedbackModal from '../components/FeedbackModal';
import DownloadButton from '../components/DownloadButton'; // 引入新的 DownloadButton 組件
import { motion } from 'framer-motion';

const TreatyArticlePage = () => {
  const { treatyId } = useParams();
  const location = useLocation();
  const [articles, setArticles] = useState([]);
  const [treatyTitle, setTreatyTitle] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [bookmarks, setBookmarks] = useState([]);
  const [showFeedbackModal, setShowFeedbackModal] = useState(false);
  const [feedbackType, setFeedbackType] = useState('');
  const [feedbackArticle, setFeedbackArticle] = useState('');

  const articleRefs = useRef({});

  useEffect(() => {
    const fetchTreatyData = async () => {
      setIsLoading(true);
      try {
        const response = await fetch(`http://localhost:3000/api/treaty/${treatyId}`);
        if (!response.ok) throw new Error('Failed to fetch treaty data');
        const data = await response.json();
        setTreatyTitle(data.title || '未命名條約');
        const transformedArticles = transformArticles(data.articles || []);
        setArticles(transformedArticles);

        if (location.hash) {
          const articleId = location.hash.slice(1);
          setTimeout(() => {
            const element = document.getElementById(articleId);
            if (element) element.scrollIntoView({ behavior: 'smooth' });
          }, 100);
        }
      } catch (error) {
        console.error("Error fetching treaty articles:", error);
        setError(error.message || '獲取數據時發生錯誤');
      } finally {
        setIsLoading(false);
      }
    };

    fetchTreatyData();
  }, [treatyId, location]);

  const transformArticles = (rawArticles) => {
    let preambleCount = 0;
    return (rawArticles || []).map(article => {
      if (article.article === "前言") {
        preambleCount++;
        return {
          ...article,
          id: `preamble-${preambleCount}`,
          displayTitle: `前言第${preambleCount}條`,
          content: Array.isArray(article.content) ? article.content : [{ text: article.content || '' }]
        };
      } else {
        return {
          ...article,
          id: `article-${article.article}`,
          displayTitle: `第${article.article}條`,
          content: Array.isArray(article.content) 
            ? article.content.map((item, index) => {
                if (typeof item !== 'string') return { text: '' };
                const match = item.match(/^（([a-z]|[ㄅ-ㄩ]|[一二三四五六七八九十]+)）(.*)/);
                if (match) {
                  return { clause: match[1], text: match[2].trim() };
                } else {
                  return { text: item };
                }
              })
            : [{ text: article.content || '' }],
          sdgs: article.sdgs || [],
          resources: article.resources || []
        };
      }
    });
  };

  const filteredArticles = articles.filter(article => {
    const titleMatch = article.displayTitle.toLowerCase().includes((searchTerm || '').toLowerCase());
    const contentMatch = article.content.some(item => 
      (item.text || '').toLowerCase().includes((searchTerm || '').toLowerCase())
    );
    return titleMatch || contentMatch;
  });

  const handleFeedback = (type, articleTitle) => {
    setFeedbackType(type);
    setFeedbackArticle(articleTitle);
    setShowFeedbackModal(true);
  };

  const handleShare = (articleId) => {
    const url = `${window.location.origin}${window.location.pathname}#${articleId}`;
    navigator.clipboard.writeText(url).then(() => {
      alert('連結已複製到剪貼簿');
    }).catch(err => {
      console.error('無法複製連結', err);
      alert('無法複製連結，請手動複製頁面 URL');
    });
  };

  const toggleBookmark = (articleId) => {
    setBookmarks(prev => 
      prev.includes(articleId) 
        ? prev.filter(id => id !== articleId)
        : [...prev, articleId]
    );
  };

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gray-900 text-white">
        <Navbar />
        <div className="container mx-auto px-4 py-8">
          <LoadingSpinner />
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-900 text-white">
        <Navbar />
        <div className="container mx-auto px-4 py-8">
          <ErrorMessage message={error} />
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-900 text-white flex flex-col">
      <Navbar />
      <div className="flex-grow container mx-auto px-4 py-8">
        <motion.h1
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="text-4xl font-bold mb-8"
        >
          {treatyTitle}
        </motion.h1>
        
        {/* 新增的下載按鈕 */}
        <div className="mb-4 flex justify-end">
          <DownloadButton treatyTitle={treatyTitle} articles={articles} />
        </div>
        
        <div className="mb-6">
          <SearchBar searchTerm={searchTerm} setSearchTerm={setSearchTerm} />
        </div>
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.5, delay: 0.4 }}
          className="space-y-6"
        >
          {filteredArticles.map((article) => (
            <ArticleContent
              key={article.id}
              article={article}
              bookmarks={bookmarks}
              toggleBookmark={toggleBookmark}
              handleShare={handleShare}
              handleFeedback={handleFeedback}
            />
          ))}
        </motion.div>
        <FeedbackModal
          isOpen={showFeedbackModal}
          onClose={() => setShowFeedbackModal(false)}
          feedbackType={feedbackType}
          feedbackArticle={feedbackArticle}
        />
      </div>
    </div>
  );
};

export default TreatyArticlePage;
