import React, { useState, useCallback } from 'react';
import { motion } from 'framer-motion';
import AIAssistant from './AIAssistant';
import Select from './Select';
import TextArea from './TextArea';
import { MessageCircle } from 'lucide-react';
const OverallAssessment = ({ assessment, updateAssessment }) => {
  const [showAI, setShowAI] = useState(false);

  const handleInputChange = useCallback((e) => {
    const { name, value } = e.target;
    updateAssessment(name, value);
  }, [updateAssessment]);

  const handleAISuggest = useCallback((suggestion) => {
    updateAssessment('overallJustification', suggestion);
    setShowAI(false);
  }, [updateAssessment]);

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <h2 className="text-2xl font-bold mb-4">總體評估</h2>
      <Select
        label="總體影響程度"
        name="overallImpact"
        value={assessment.overallImpact}
        onChange={handleInputChange}
        options={[
          { value: '', label: '選擇總體影響程度' },
          { value: '低', label: '低' },
          { value: '中', label: '中' },
          { value: '高', label: '高' },
        ]}
      />
      <div className="mb-4 relative">
        <label className="block text-sm font-medium text-gray-200 mb-1">評估理由</label>
        <textarea
          name="overallJustification"
          value={assessment.overallJustification}
          onChange={handleInputChange}
          className="w-full p-2 bg-gray-700 border border-gray-600 rounded text-white pr-10 transition duration-300 focus:border-blue-500 focus:ring focus:ring-blue-200 focus:ring-opacity-50"
          rows={6}
          placeholder="請提供總體評估的理由和依據..."
        />
        <button
          onClick={() => setShowAI(true)}
          className="absolute right-2 top-8 text-blue-400 hover:text-blue-300 transition duration-300"
          title="獲取 AI 建議"
        >
          <MessageCircle size={20} />
        </button>
      </div>
      <TextArea
        label="主要發現摘要"
        name="keySummary"
        value={assessment.keySummary}
        onChange={handleInputChange}
        rows={6}
        placeholder="總結評估過程中的主要發現..."
      />
      <TextArea
        label="建議後續行動"
        name="recommendedActions"
        value={assessment.recommendedActions}
        onChange={handleInputChange}
        rows={6}
        placeholder="列出建議的後續行動和步驟..."
      />
      {showAI && (
        <AIAssistant onSuggest={handleAISuggest} onClose={() => setShowAI(false)} />
      )}
    </motion.div>
  );
};

export default OverallAssessment;
