import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { X, MessageCircle } from 'lucide-react';


const AIAssistant = ({ onSuggest, onClose }) => {
  const [query, setQuery] = useState('');
  const [suggestion, setSuggestion] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    const aiResponse = await mockAIAPI(query);
    setSuggestion(aiResponse);
  };

  const mockAIAPI = async (query) => {
    await new Promise(resolve => setTimeout(resolve, 1000));
    return `這是對於 "${query}" 的 AI 建議。在實際應用中，這裡將是基於您的查詢生成的有意義的建議。`;
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: 20 }}
      className="fixed bottom-4 right-4 w-80 bg-gray-800 rounded-lg shadow-lg p-4 z-50"
    >
      <button onClick={onClose} className="absolute top-2 right-2 text-gray-400 hover:text-white">
        <X size={20} />
      </button>
      <h3 className="text-lg font-semibold mb-2">AI 助手</h3>
      <form onSubmit={handleSubmit} className="mb-4">
        <input
          type="text"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          placeholder="詢問 AI 助手..."
          className="w-full p-2 bg-gray-700 border border-gray-600 rounded text-white mb-2"
        />
        <button type="submit" className="w-full bg-blue-500 hover:bg-blue-600 text-white py-2 rounded transition duration-300">
          獲取建議
        </button>
      </form>
      {suggestion && (
        <div className="bg-gray-700 p-2 rounded">
          <p className="text-sm text-gray-300">{suggestion}</p>
          <button
            onClick={() => onSuggest(suggestion)}
            className="mt-2 text-blue-400 hover:text-blue-300 text-sm transition duration-300"
          >
            使用這個建議
          </button>
        </div>
      )}
    </motion.div>
  );
};

export default AIAssistant;
