import React, { useState, useCallback } from 'react';
import { motion } from 'framer-motion';
import AIAssistant from './AIAssistant';
import { MessageCircle } from 'lucide-react';

const MonitoringPlan = ({ assessment, updateAssessment }) => {
  const [showAI, setShowAI] = useState(false);

  const handleInputChange = useCallback((e) => {
    updateAssessment('monitoringPlan', e.target.value);
  }, [updateAssessment]);

  const handleAISuggest = useCallback((suggestion) => {
    updateAssessment('monitoringPlan', suggestion);
    setShowAI(false);
  }, [updateAssessment]);

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <h2 className="text-2xl font-bold mb-4">監測計劃</h2>
      <div className="mb-4 relative">
        <label className="block text-sm font-medium text-gray-200 mb-1">
          描述如何監測和評估緩解措施的有效性
        </label>
        <textarea
          value={assessment.monitoringPlan}
          onChange={handleInputChange}
          className="w-full p-2 bg-gray-700 border border-gray-600 rounded text-white pr-10 transition duration-300 focus:border-blue-500 focus:ring focus:ring-blue-200 focus:ring-opacity-50"
          rows={8}
          placeholder="詳細描述監測計劃..."
        />
        <button
          onClick={() => setShowAI(true)}
          className="absolute right-2 top-8 text-blue-400 hover:text-blue-300 transition duration-300"
          title="獲取 AI 建議"
        >
          <MessageCircle size={20} />
        </button>
      </div>
      {showAI && (
        <AIAssistant onSuggest={handleAISuggest} onClose={() => setShowAI(false)} />
      )}
    </motion.div>
  );
};

export default MonitoringPlan;
