import React from 'react';

const FeatureCard = ({ icon: Icon, title, description, delay, isVisible }) => (
    <div
      className={`bg-gray-800 rounded-lg shadow-xl p-6 hover:bg-gray-700 transition duration-300 transform hover:-translate-y-2 ${
        isVisible ? 'opacity-100' : 'opacity-0'
      }`}
      style={{ transitionDelay: `${delay}ms` }}
    >
      <Icon size={48} className="text-blue-400 mb-4" />
      <h3 className="text-xl font-semibold mb-2">{title}</h3>
      <p className="text-gray-400">{description}</p>
    </div>
  );

export default FeatureCard;
