import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { Search, Filter, Calendar, Users, Tag, ChevronDown } from 'lucide-react';
import Navbar from '../components/Navbar';

const AdvancedSearchPage = () => {
  const [searchParams, setSearchParams] = useState({
    keyword: '',
    dateFrom: '',
    dateTo: '',
    meetingType: '',
    humanRightsRelevance: '',
    tags: []
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setSearchParams(prev => ({ ...prev, [name]: value }));
  };

  const handleSearch = (e) => {
    e.preventDefault();
    console.log('Search with params:', searchParams);
    // Implement search logic here
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900 text-white">
      <Navbar />
      <div className="container mx-auto px-4 py-12">
        <motion.h1 
          className="text-4xl font-bold mb-8 text-center"
          initial={{ opacity: 0, y: -30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          進階搜尋
        </motion.h1>

        <motion.form 
          onSubmit={handleSearch}
          className="bg-gray-800 rounded-lg shadow-xl p-8 max-w-4xl mx-auto"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.2, duration: 0.5 }}
        >
          <div className="mb-6">
            <label className="block text-gray-300 mb-2" htmlFor="keyword">關鍵字搜尋</label>
            <div className="relative">
              <input
                type="text"
                id="keyword"
                name="keyword"
                value={searchParams.keyword}
                onChange={handleInputChange}
                className="w-full bg-gray-700 text-white rounded-lg pl-10 pr-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="輸入關鍵字..."
              />
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
            <div>
              <label className="block text-gray-300 mb-2" htmlFor="dateFrom">開始日期</label>
              <div className="relative">
                <input
                  type="date"
                  id="dateFrom"
                  name="dateFrom"
                  value={searchParams.dateFrom}
                  onChange={handleInputChange}
                  className="w-full bg-gray-700 text-white rounded-lg pl-10 pr-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
                <Calendar className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
              </div>
            </div>
            <div>
              <label className="block text-gray-300 mb-2" htmlFor="dateTo">結束日期</label>
              <div className="relative">
                <input
                  type="date"
                  id="dateTo"
                  name="dateTo"
                  value={searchParams.dateTo}
                  onChange={handleInputChange}
                  className="w-full bg-gray-700 text-white rounded-lg pl-10 pr-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
                <Calendar className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
              </div>
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
            <div>
              <label className="block text-gray-300 mb-2" htmlFor="meetingType">會議類型</label>
              <div className="relative">
                <select
                  id="meetingType"
                  name="meetingType"
                  value={searchParams.meetingType}
                  onChange={handleInputChange}
                  className="w-full bg-gray-700 text-white rounded-lg pl-10 pr-4 py-2 appearance-none focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="">全部類型</option>
                  <option value="校務會議">校務會議</option>
                  <option value="院務會議">院務會議</option>
                  <option value="系務會議">系務會議</option>
                  <option value="其他會議">其他會議</option>
                </select>
                <Users className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
                <ChevronDown className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
              </div>
            </div>
            <div>
              <label className="block text-gray-300 mb-2" htmlFor="humanRightsRelevance">人權相關度</label>
              <div className="relative">
                <select
                  id="humanRightsRelevance"
                  name="humanRightsRelevance"
                  value={searchParams.humanRightsRelevance}
                  onChange={handleInputChange}
                  className="w-full bg-gray-700 text-white rounded-lg pl-10 pr-4 py-2 appearance-none focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="">全部</option>
                  <option value="high">高</option>
                  <option value="medium">中</option>
                  <option value="low">低</option>
                </select>
                <Filter className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
                <ChevronDown className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
              </div>
            </div>
          </div>

          <div className="mb-6">
            <label className="block text-gray-300 mb-2">標籤</label>
            <div className="flex flex-wrap gap-2">
              {['人權', '性別平等', '學術自由', '隱私權', '言論自由'].map((tag) => (
                <motion.button
                  key={tag}
                  type="button"
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  className={`px-3 py-1 rounded-full text-sm ${
                    searchParams.tags.includes(tag)
                      ? 'bg-blue-500 text-white'
                      : 'bg-gray-700 text-gray-300 hover:bg-gray-600'
                  }`}
                  onClick={() => {
                    setSearchParams(prev => ({
                      ...prev,
                      tags: prev.tags.includes(tag)
                        ? prev.tags.filter(t => t !== tag)
                        : [...prev.tags, tag]
                    }));
                  }}
                >
                  {tag}
                </motion.button>
              ))}
            </div>
          </div>

          <motion.button
            type="submit"
            className="w-full bg-blue-500 hover:bg-blue-600 text-white font-bold py-3 px-6 rounded-lg transition duration-300 flex items-center justify-center"
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
          >
            <Search className="mr-2" />
            進行搜尋
          </motion.button>
        </motion.form>
      </div>
    </div>
  );
};

export default AdvancedSearchPage;