import React from 'react';

const Input = ({ label, ...props }) => (
  <div className="mb-4">
    <label className="block text-sm font-medium text-gray-200 mb-1">{label}</label>
    <input
      {...props}
      className="w-full p-2 bg-gray-700 border border-gray-600 rounded text-white transition duration-300 focus:border-blue-500 focus:ring focus:ring-blue-200 focus:ring-opacity-50"
    />
  </div>
);

export default Input;
