import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { ArrowLeft, Calendar, Clock, Users, MapPin,  Download } from 'lucide-react';
import Navbar from '../components/Navbar';

const MeetingRecordDetail = () => {
  const { id } = useParams();
  const [meeting, setMeeting] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [activeTab, setActiveTab] = useState('agenda');
  const [showAttendees, setShowAttendees] = useState(false);
  const [showProposerInfo, setShowProposerInfo] = useState(null);

  useEffect(() => {
    fetchMeetingDetails();
  }, [id]);

  const fetchMeetingDetails = async () => {
    setLoading(true);
    try {
      // 模擬 API 調用
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // 模擬詳細的會議數據
      const meetingData = {
        id: 'm2',
        title: '學生會第三次常務會議',
        date: '2024-03-18',
        time: '14:00-16:30',
        location: '學生活動中心 203 會議室',
        type: 'regular',
        department: '學生會',
        attendees: [
          { id: 1, name: '王小明', role: '學生會長', avatar: '👨‍🎓' },
          { id: 2, name: '李小華', role: '副會長', avatar: '👩‍🎓' },
          { id: 3, name: '張小農', role: '總務長', avatar: '🧑‍🎓' },
          { id: 4, name: '陳小杰', role: '學權部長', avatar: '👨‍🎓' },
          { id: 5, name: '林小美', role: '活動部長', avatar: '👩‍🎓' },
        ],
        chairReport: "各位同學好，感謝大家出席本次會議。首先，我要報告上次會議決議事項的執行進度...",
        agenda: [
          {
            id: 'a1',
            title: '審議學生活動中心使用規範修訂案',
            proposer: { id: 1, name: '王小明', role: '學生會長', avatar: '👨‍🎓', bio: '王小明是本屆學生會長，主修公共政策，致力於促進校園民主與學生權益。' },
            content: '因應近期學生活動中心使用頻率增加，建議修訂現有使用規範，增加對場地維護和時間管理的要求。',
            explanation: '現行規範已使用三年，部分條款不符合當前需求。本次修訂主要涉及使用時間限制、場地清潔要求、以及違規處理方式等方面。',
            discussion: [
              { speaker: '副會長 李小華', content: '支持修訂，但建議在時間限制方面增加彈性，特殊情況下可申請延長使用時間。' },
              { speaker: '總務長 張小農', content: '贊同修訂，特別是加強對場地維護的要求。建議增加使用後檢查和評分機制。' },
              { speaker: '學權部長 陳小杰', content: '對於違規處理方式，建議採取漸進式懲罰制度，給予學生更多改正的機會。' }
            ],
            voting: {
              for: 12,
              against: 1,
              abstain: 2
            },
            result: '通過',
            attachments: [
              { name: '學生活動中心使用規範修訂草案.pdf', url: '/attachments/draft.pdf' },
              { name: '場地使用統計數據.xlsx', url: '/attachments/stats.xlsx' }
            ]
          },
          // ... 其他議程項目
        ],
        adhocMotions: [
          {
            id: 'm1',
            title: '提議成立學生權益調查小組',
            proposer: { id: 4, name: '陳小杰', role: '學權部長', avatar: '👨‍🎓', bio: '陳小杰現任學權部長，法律系三年級學生，熱衷於維護學生權益。' },
            content: '鑒於近期收到多起學生權益受損反饋，建議成立專門小組進行全面調查。',
            discussion: [
              { speaker: '學生會長 王小明', content: '支持這個提議，但需要明確小組的具體職責和運作方式。' },
              { speaker: '副會長 李小華', content: '贊同成立小組，建議由學權部牽頭，各部門選派代表參與。' }
            ],
            voting: {
              for: 14,
              against: 0,
              abstain: 1
            },
            result: '通過，責成學權部一週內提交小組組建方案',
            attachments: [
              { name: '學生權益調查小組提案.pdf', url: '/attachments/proposal.pdf' }
            ]
          }
        ]
      };

      setMeeting(meetingData);
    } catch (error) {
      console.error("Error fetching meeting details:", error);
      setError("無法載入會議記錄詳情。請稍後再試。");
    } finally {
      setLoading(false);
    }
  };

  const renderVotingResult = (voting) => {
    const total = voting.for + voting.against + voting.abstain;
    return (
      <div className="bg-gray-700 rounded-lg p-4 mb-4">
        <p className="text-blue-300 font-semibold mb-2">表決結果</p>
        <div className="flex justify-between text-gray-300 mb-2">
          <span>贊成: {voting.for}</span>
          <span>反對: {voting.against}</span>
          <span>棄權: {voting.abstain}</span>
        </div>
        <div className="w-full bg-gray-600 rounded-full h-2.5 dark:bg-gray-700 mb-1">
          <div className="bg-green-600 h-2.5 rounded-full" style={{width: `${(voting.for / total) * 100}%`}}></div>
        </div>
        <div className="w-full bg-gray-600 rounded-full h-2.5 dark:bg-gray-700 mb-1">
          <div className="bg-red-600 h-2.5 rounded-full" style={{width: `${(voting.against / total) * 100}%`}}></div>
        </div>
        <div className="w-full bg-gray-600 rounded-full h-2.5 dark:bg-gray-700">
          <div className="bg-yellow-600 h-2.5 rounded-full" style={{width: `${(voting.abstain / total) * 100}%`}}></div>
        </div>
      </div>
    );
  };

  const renderContent = () => {
    if (loading) {
      return (
        <div className="flex items-center justify-center h-[calc(100vh-64px)]">
          <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2 border-blue-500"></div>
        </div>
      );
    }

    if (error) {
      return (
        <div className="container mx-auto px-4 py-8">
          <div className="bg-red-900 border-l-4 border-red-500 text-red-200 p-4 rounded" role="alert">
            <p className="font-bold">錯誤</p>
            <p>{error}</p>
          </div>
        </div>
      );
    }

    if (!meeting) {
      return null;
    }

    return (
      <div className="container mx-auto px-4 py-8">
        <Link to="/meeting-records" className="flex items-center text-blue-400 hover:text-blue-300 mb-6">
          <ArrowLeft size={20} className="mr-2" />
          返回會議列表
        </Link>

        <div className="bg-gray-800 rounded-lg shadow-lg p-6 mb-8">
          <h1 className="text-3xl font-bold text-white mb-4">{meeting.title}</h1>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-gray-300">
            <div className="flex items-center">
              <Calendar size={18} className="mr-2 text-blue-400" />
              <span>{meeting.date}</span>
            </div>
            <div className="flex items-center">
              <Clock size={18} className="mr-2 text-blue-400" />
              <span>{meeting.time}</span>
            </div>
            <div className="flex items-center">
              <MapPin size={18} className="mr-2 text-blue-400" />
              <span>{meeting.location}</span>
            </div>
            <div className="flex items-center cursor-pointer" onClick={() => setShowAttendees(true)}>
              <Users size={18} className="mr-2 text-blue-400" />
              <span>出席人數: {meeting.attendees.length}</span>
            </div>
          </div>
        </div>

        {showAttendees && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-gray-800 rounded-lg p-6 max-w-md w-full">
              <h2 className="text-2xl font-bold text-white mb-4">出席人員</h2>
              {meeting.attendees.map(attendee => (
                <div key={attendee.id} className="flex items-center mb-2">
                  <span className="mr-2 text-2xl">{attendee.avatar}</span>
                  <span className="text-white">{attendee.name} - {attendee.role}</span>
                </div>
              ))}
              <button
                className="mt-4 bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-4 rounded"
                onClick={() => setShowAttendees(false)}
              >
                關閉
              </button>
            </div>
          </div>
        )}

        <div className="bg-gray-800 rounded-lg shadow-lg p-6 mb-8">
          <h2 className="text-2xl font-bold text-white mb-4">主席報告</h2>
          <p className="text-gray-300">{meeting.chairReport}</p>
        </div>

        <div className="mb-8">
          <div className="flex border-b border-gray-700">
            <button
              className={`py-2 px-4 ${activeTab === 'agenda' ? 'text-blue-500 border-b-2 border-blue-500' : 'text-gray-400'}`}
              onClick={() => setActiveTab('agenda')}
            >
              議程
            </button>
            <button
              className={`py-2 px-4 ${activeTab === 'adhocMotions' ? 'text-blue-500 border-b-2 border-blue-500' : 'text-gray-400'}`}
              onClick={() => setActiveTab('adhocMotions')}
            >
              臨時動議
            </button>
          </div>
        </div>

        <AnimatePresence mode="wait">
          {activeTab === 'agenda' && (
            <motion.div
              key="agenda"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.3 }}
            >
              {meeting.agenda.map((item, index) => (
                <div key={item.id} className="bg-gray-800 rounded-lg shadow-lg p-6 mb-6">
                  <h2 className="text-2xl font-semibold text-white mb-4">
                    {index + 1}. {item.title}
                  </h2>
                  <div className="bg-gray-700 rounded-lg p-4 mb-4">
                    <p className="text-blue-300 font-semibold mb-2">提案人</p>
                    <div className="flex items-center cursor-pointer" onClick={() => setShowProposerInfo(item.proposer)}>
                      <span className="mr-2 text-2xl">{item.proposer.avatar}</span>
                      <span className="text-gray-300">{item.proposer.name} - {item.proposer.role}</span>
                    </div>
                  </div>
                  <div className="bg-gray-700 rounded-lg p-4 mb-4">
                    <p className="text-blue-300 font-semibold mb-2">提案內容</p>
                    <p className="text-gray-300">{item.content}</p>
                  </div>
                  <div className="bg-gray-700 rounded-lg p-4 mb-4">
                    <p className="text-blue-300 font-semibold mb-2">說明</p>
                    <p className="text-gray-300">{item.explanation}</p>
                  </div>
                  <div className="bg-gray-700 rounded-lg p-4 mb-4">
                    <p className="text-blue-300 font-semibold mb-2">討論過程</p>
                    {item.discussion.map((discussion, dIndex) => (
                      <div key={dIndex} className="mb-2">
                        <p className="text-green-400 font-semibold">{discussion.speaker}</p>
                        <p className="text-gray-300">{discussion.content}</p>
                      </div>
                    ))}
                  </div>
                  {renderVotingResult(item.voting)}
                  <div className="bg-blue-900 text-blue-200 rounded-lg p-4 mb-4">
                    <p className="font-semibold">決議: {item.result}</p>
                  </div>
                  {item.attachments && item.attachments.length > 0 && (
                    <div className="bg-gray-700 rounded-lg p-4">
                      <p className="text-blue-300 font-semibold mb-2">附件</p>
                      {item.attachments.map((attachment, aIndex) => (
                        <a 
                          key={aIndex}
                          href={attachment.url}
                          className="flex items-center text-gray-300 hover:text-blue-300 mb-2"
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          <Download size={18} className="mr-2" />
                          {attachment.name}
                        </a>
                      ))}
                    </div>
                  )}
                </div>
              ))}
            </motion.div>
          )}

          {activeTab === 'adhocMotions' && (
            <motion.div
              key="adhocMotions"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.3 }}
            >
              {meeting.adhocMotions.map((motion, index) => (
                <div key={motion.id} className="bg-gray-800 rounded-lg shadow-lg p-6 mb-6">
                  <h2 className="text-2xl font-semibold text-white mb-4">
                    臨時動議 {index + 1}: {motion.title}
                  </h2>
                  <div className="bg-gray-700 rounded-lg p-4 mb-4">
                    <p className="text-blue-300 font-semibold mb-2">提案人</p>
                    <div className="flex items-center cursor-pointer" onClick={() => setShowProposerInfo(motion.proposer)}>
                      <span className="mr-2 text-2xl">{motion.proposer.avatar}</span>
                      <span className="text-gray-300">{motion.proposer.name} - {motion.proposer.role}</span>
                    </div>
                  </div>
                  <div className="bg-gray-700 rounded-lg p-4 mb-4">
                    <p className="text-blue-300 font-semibold mb-2">提案內容</p>
                    <p className="text-gray-300">{motion.content}</p>
                  </div>
                  <div className="bg-gray-700 rounded-lg p-4 mb-4">
                    <p className="text-blue-300 font-semibold mb-2">討論過程</p>
                    {motion.discussion.map((discussion, dIndex) => (
                      <div key={dIndex} className="mb-2">
                        <p className="text-green-400 font-semibold">{discussion.speaker}</p>
                        <p className="text-gray-300">{discussion.content}</p>
                      </div>
                    ))}
                  </div>
                  {renderVotingResult(motion.voting)}
                  <div className="bg-blue-900 text-blue-200 rounded-lg p-4 mb-4">
                    <p className="font-semibold">決議: {motion.result}</p>
                  </div>
                  {motion.attachments && motion.attachments.length > 0 && (
                    <div className="bg-gray-700 rounded-lg p-4">
                      <p className="text-blue-300 font-semibold mb-2">附件</p>
                      {motion.attachments.map((attachment, aIndex) => (
                        <a 
                          key={aIndex}
                          href={attachment.url}
                          className="flex items-center text-gray-300 hover:text-blue-300 mb-2"
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          <Download size={18} className="mr-2" />
                          {attachment.name}
                        </a>
                      ))}
                    </div>
                  )}
                </div>
              ))}
            </motion.div>
          )}
        </AnimatePresence>

        {showProposerInfo && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-gray-800 rounded-lg p-6 max-w-md w-full">
              <h2 className="text-2xl font-bold text-white mb-4">提案人資訊</h2>
              <div className="flex items-center mb-4">
                <span className="text-4xl mr-4">{showProposerInfo.avatar}</span>
                <div>
                  <p className="text-white font-semibold">{showProposerInfo.name}</p>
                  <p className="text-gray-300">{showProposerInfo.role}</p>
                </div>
              </div>
              <p className="text-gray-300 mb-4">{showProposerInfo.bio}</p>
              <button
                className="bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-4 rounded"
                onClick={() => setShowProposerInfo(null)}
              >
                關閉
              </button>
            </div>
          </div>
        )}
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 to-gray-800 text-white">
      <Navbar />
      {renderContent()}
    </div>
  );
};

export default MeetingRecordDetail;