import React from 'react';
import { motion } from 'framer-motion';
import { Calendar, Users, Globe, ChevronDown, ChevronUp, ExternalLink } from 'lucide-react';
import { Link } from 'react-router-dom';

const TreatyCard = ({ treaty, handleTreatySelection, selectedTreaties }) => {
  const [expanded, setExpanded] = React.useState(false);

  if (!treaty) {
    return <div className="p-4 bg-red-100 text-red-700 rounded">Error: Treaty data is missing</div>;
  }

  const isSelected = selectedTreaties.includes(treaty._id);

  const cardVariants = {
    hover: { scale: 1.02, transition: { duration: 0.3 } },
    tap: { scale: 0.98, transition: { duration: 0.3 } },
  };

  const safelyGetData = (data, fallback = 'N/A') => data || fallback;
  const safelyJoinArray = (arr, fallback = 'N/A') => Array.isArray(arr) ? arr.join(', ') : fallback;

  return (
    <motion.div
      className={`bg-gradient-to-br from-gray-800 to-gray-900 rounded-xl overflow-hidden shadow-lg transition-all duration-300 ${
        isSelected ? 'ring-2 ring-blue-500' : ''
      }`}
      variants={cardVariants}
      whileHover="hover"
      whileTap="tap"
    >
      <div className="p-6">
        <div className="flex justify-between items-start mb-4">
          <h2 className="text-2xl font-bold text-white mb-2">{safelyGetData(treaty.title)}</h2>
          <span className="px-3 py-1 bg-blue-500 text-white text-xs font-semibold rounded-full">
            {safelyGetData(treaty.category)}
          </span>
        </div>
        
        <div className="space-y-2 mb-4">
          <div className="flex items-center text-gray-300">
            <Calendar className="w-4 h-4 mr-2" />
            <span>{treaty.date ? new Date(treaty.date).toLocaleDateString() : 'Date not available'}</span>
          </div>
          <div className="flex items-center text-gray-300">
            <Users className="w-4 h-4 mr-2" />
            <span>{safelyJoinArray(treaty.parties)}</span>
          </div>
          <div className="flex items-center text-gray-300">
            <Globe className="w-4 h-4 mr-2" />
            <span>{safelyGetData(treaty.status)}</span>
          </div>
        </div>

        <div className={`overflow-hidden transition-all duration-300 ${expanded ? 'max-h-96' : 'max-h-20'}`}>
          <p className="text-gray-400 text-sm">{safelyGetData(treaty.description)}</p>
        </div>

        <div className="mt-4 flex justify-between items-center">
          <button
            onClick={() => setExpanded(!expanded)}
            className="text-blue-400 hover:text-blue-300 transition duration-300 flex items-center focus:outline-none"
          >
            {expanded ? (
              <>
                收起 <ChevronUp className="ml-1 w-4 h-4" />
              </>
            ) : (
              <>
                展開 <ChevronDown className="ml-1 w-4 h-4" />
              </>
            )}
          </button>
          <div className="flex space-x-2">
            <Link
              to={`/treaty/${treaty._id}`}
              className="px-4 py-2 bg-green-600 text-white rounded-full text-sm font-semibold hover:bg-green-700 transition duration-300 flex items-center"
            >
              詳細 <ExternalLink className="ml-1 w-4 h-4" />
            </Link>
            <button
              onClick={() => handleTreatySelection(treaty._id)}
              className={`px-4 py-2 rounded-full text-sm font-semibold transition duration-300 ${
                isSelected
                  ? 'bg-blue-600 text-white hover:bg-blue-700'
                  : 'bg-gray-700 text-gray-300 hover:bg-gray-600'
              }`}
            >
              {isSelected ? '取消選擇' : '選擇'}
            </button>
          </div>
        </div>
      </div>
    </motion.div>
  );
};

export default TreatyCard;