import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Search, Calendar, Clock, Users, Tag } from 'lucide-react';
import { Link } from 'react-router-dom';
import Navbar from '../components/Navbar';

const MeetingRecordsList = () => {
  const [meetings, setMeetings] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [filter, setFilter] = useState('all');

  useEffect(() => {
    fetchMeetings();
  }, []);

  const fetchMeetings = async () => {
    setLoading(true);
    try {
      // 模擬 API 調用
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      const meetingsData = [
        {
          id: 'm1',
          title: '學生議會第一次全體會議',
          date: '2024-03-15',
          time: '14:00-16:00',
          type: 'regular',
          department: '學生議會',
          attendees: 25
        },
        {
          id: 'm2',
          title: '行政中心緊急預算會議',
          date: '2024-03-18',
          time: '10:00-12:00',
          type: 'emergency',
          department: '行政中心',
          attendees: 10
        },
        {
          id: 'm3',
          title: '學權部例行工作會議',
          date: '2024-03-20',
          time: '15:30-17:00',
          type: 'regular',
          department: '學權部',
          attendees: 8
        },
        {
          id: 'm4',
          title: '學生會與校方協調會議',
          date: '2024-03-25',
          time: '13:00-15:00',
          type: 'special',
          department: '學生會',
          attendees: 15
        },
        {
          id: 'm5',
          title: '活動部跨校聯合活動籌備會',
          date: '2024-03-28',
          time: '16:00-18:00',
          type: 'regular',
          department: '活動部',
          attendees: 12
        }
      ];

      setMeetings(meetingsData);
    } catch (error) {
      console.error("Error fetching meetings:", error);
      setError("無法載入會議記錄。請稍後再試。");
    } finally {
      setLoading(false);
    }
  };

  const filteredMeetings = meetings.filter(meeting => 
    (meeting.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
     meeting.department.toLowerCase().includes(searchTerm.toLowerCase())) &&
    (filter === 'all' || meeting.type === filter)
  );

  const getTypeStyle = (type) => {
    switch (type) {
      case 'emergency':
        return 'bg-red-900 text-red-200 border-red-700';
      case 'special':
        return 'bg-purple-900 text-purple-200 border-purple-700';
      default:
        return 'bg-blue-900 text-blue-200 border-blue-700';
    }
  };

  const MeetingCard = ({ meeting }) => (
    <motion.div
      layout
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      className="bg-gray-800 rounded-lg shadow-md overflow-hidden transition-all duration-300 hover:shadow-lg hover:bg-gray-750 border border-gray-700"
    >
      <div className="p-6">
        <div className="flex justify-between items-start mb-4">
          <h3 className="text-xl font-semibold text-white">{meeting.title}</h3>
          <span className={`px-3 py-1 rounded-full text-xs font-medium ${getTypeStyle(meeting.type)}`}>
            {meeting.type === 'emergency' ? '緊急' : meeting.type === 'special' ? '特別' : '例行'}
          </span>
        </div>
        <div className="grid grid-cols-2 gap-4 text-sm text-gray-300 mb-4">
          <div className="flex items-center">
            <Calendar size={16} className="mr-2 text-gray-400" />
            {meeting.date}
          </div>
          <div className="flex items-center">
            <Clock size={16} className="mr-2 text-gray-400" />
            {meeting.time}
          </div>
          <div className="flex items-center">
            <Tag size={16} className="mr-2 text-gray-400" />
            {meeting.department}
          </div>
          <div className="flex items-center">
            <Users size={16} className="mr-2 text-gray-400" />
            出席人數: {meeting.attendees}
          </div>
        </div>
        <Link
          to={`/meeting-records/${meeting.id}`}
          className="inline-block px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors duration-300"
        >
          查看會議記錄
        </Link>
      </div>
    </motion.div>
  );

  const renderContent = () => {
    if (loading) {
      return (
        <div className="flex items-center justify-center h-[calc(100vh-64px)]">
          <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2 border-blue-500"></div>
        </div>
      );
    }

    if (error) {
      return (
        <div className="container mx-auto px-4 py-8">
          <div className="bg-red-900 border-l-4 border-red-500 text-red-200 p-4 rounded" role="alert">
            <p className="font-bold">錯誤</p>
            <p>{error}</p>
          </div>
        </div>
      );
    }

    return (
      <div className="container mx-auto px-4 py-8">
        <div className="flex flex-col md:flex-row justify-between items-center mb-8">
          <h1 className="text-4xl font-bold mb-4 md:mb-0 text-white">學生會會議記錄</h1>
          <div className="flex items-center space-x-4">
            <div className="relative">
              <input
                type="text"
                placeholder="搜尋會議..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full md:w-64 p-2 pl-10 pr-4 rounded-md bg-gray-700 text-white placeholder-gray-400 border border-gray-600 focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={18} />
            </div>
            <select
              value={filter}
              onChange={(e) => setFilter(e.target.value)}
              className="bg-gray-700 text-white border border-gray-600 rounded-md px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="all">所有會議</option>
              <option value="regular">例行會議</option>
              <option value="emergency">緊急會議</option>
              <option value="special">特別會議</option>
            </select>
          </div>
        </div>

        <motion.div layout className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
          <AnimatePresence>
            {filteredMeetings.map(meeting => (
              <MeetingCard key={meeting.id} meeting={meeting} />
            ))}
          </AnimatePresence>
        </motion.div>

        {filteredMeetings.length === 0 && (
          <div className="text-center py-12">
            <p className="text-2xl text-gray-400">沒有找到符合的會議記錄</p>
            <p className="text-gray-500 mt-2">請嘗試調整搜尋條件或篩選器</p>
          </div>
        )}
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 to-gray-800">
      <Navbar />
      {renderContent()}
    </div>
  );
};

export default MeetingRecordsList;