import React from 'react';
import { Users } from 'lucide-react';
import { motion } from 'framer-motion';


const CommunityHighlights = ({ communityHighlights }) => (
  <motion.div
    initial={{ opacity: 0, x: 20 }}
    animate={{ opacity: 1, x: 0 }}
    transition={{ duration: 0.5, delay: 0.4 }}
    className="bg-gray-800 rounded-lg p-6 shadow-lg"
  >
    <h3 className="text-2xl font-bold mb-6 flex items-center">
      <Users className="mr-2" />
      社群活動
    </h3>
    <ul className="space-y-4">
      {communityHighlights.map((event, index) => (
        <li key={index} className="flex justify-between items-center p-3 bg-gray-700 rounded-lg hover:bg-gray-600 transition duration-300">
          <div>
            <h4 className="font-semibold text-blue-400">{event.title}</h4>
            <p className="text-sm text-gray-400">{event.date}</p>
          </div>
          <span className="bg-blue-500 text-white px-2 py-1 rounded-full text-xs">
            {event.participants} 參與者
          </span>
        </li>
      ))}
    </ul>
  </motion.div>
);

export default CommunityHighlights;
