import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import TreatyListPage from './pages/TreatyListPage';
import TreatyArticlePage from './pages/TreatyArticlePage';
import HomePage from './pages/HomePage';
import SDGDetailPage from './pages/SDGDetailPage';
import MeetingListPage from './pages/MeetingListPage';
import MeetingDetailPage from './pages/MeetingDetailPage';
import HumanRightsImpactAssessment from './pages/HumanRightsImpactAssessment';
import AboutUsPage from './pages/AboutUsPage';
import AdvancedSearchPage from './pages/AdvancedSearchPage';
import LawListPage from './pages/LawListPage';
import LawDetailPage from './pages/LawDetailPage';
import MeetingRecordsList from './pages/MeetingRecordsList';
import MeetingRecordDetail from './pages/MeetingRecordDetail';
import StudentUnionPersonnelManagement from './pages/admin/StudentUnionPersonnelManagement';

function App() {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/treaty" element={<TreatyListPage />} />
        <Route path="/treaty/:treatyId" element={<TreatyArticlePage />} />
        <Route path="/sdg/:sdgNumber" element={<SDGDetailPage/>} />
        <Route path="/meetings" element={<MeetingListPage />} />
        <Route path="/meetings/:id" element={<MeetingDetailPage />} />
        <Route path="/impact-assessment" element={<HumanRightsImpactAssessment />} />
        <Route path="/about" element={<AboutUsPage />} />
        <Route path="/advanced-search" element={<AdvancedSearchPage />} />
        <Route path="/laws" element={<LawListPage />} />
        <Route path="/law/:id" element={<LawDetailPage />} />
        <Route path="/meeting-records" element={<MeetingRecordsList />} />
        <Route path="/meeting-records/:id" element={<MeetingRecordDetail />} />
        <Route path="/admin/student-union-personnel" element={<StudentUnionPersonnelManagement />} />
      </Routes>
    </Router>
  );
}

export default App;
