import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';

const FeedbackModal = ({ isOpen, onClose, feedbackType, feedbackArticle }) => (
  <AnimatePresence>
    {isOpen && (
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center"
      >
        <motion.div
          initial={{ y: -50, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          exit={{ y: -50, opacity: 0 }}
          className="bg-gray-800 p-6 rounded-lg w-96"
        >
          <h2 className="text-xl font-bold mb-4">
            {feedbackType === 'error' ? '報錯' : '提供建議'}：{feedbackArticle}
          </h2>
          <textarea
            className="w-full h-32 p-2 mb-4 bg-gray-700 text-white rounded"
            placeholder={feedbackType === 'error' ? '請描述您發現的錯誤...' : '請提供您的建議...'}
          ></textarea>
          <div className="flex justify-end space-x-2">
            <button
              onClick={onClose}
              className="px-4 py-2 bg-gray-600 text-white rounded"
            >
              取消
            </button>
            <button
              onClick={() => {
                // Here you would handle the feedback submission
                console.log('Feedback submitted');
                onClose();
              }}
              className="px-4 py-2 bg-blue-500 text-white rounded"
            >
              提交
            </button>
          </div>
        </motion.div>
      </motion.div>
    )}
  </AnimatePresence>
);

export default FeedbackModal;
