import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { motion } from 'framer-motion';
import { Globe, Target, BarChart, Users, Lightbulb, BookOpen } from 'lucide-react';
import { Link } from 'react-router-dom';
import Navbar from '../components/Navbar';
import LoadingSpinner from '../components/LoadingSpinner';
import ErrorMessage from '../components/ErrorMessage';

const sdgExample = {
  number: 1,
  title: "消除貧窮",
  description: "在全球範圍內消除一切形式的貧窮。",
  keyIndicators: [
    "每人每天收入低於1.25美元的比例",
    "國家貧困線以下的人口比例",
    "享有社會保障的比例"
  ],
  targetGroups: "低收入家庭、貧困地區居民",
  actionItems: [
    "推廣社會保障措施，確保基本生活保障。",
    "支持貧困社區的基礎設施建設和公共服務。",
    "推動就業機會，特別是在貧困地區。"
  ],
  relatedArticles: [
    { id: 'article-1', title: '第1條', content: '關於消除貧窮的條文內容...' },
    { id: 'article-5', title: '第5條', content: '關於社會保障的條文內容...' },
  ]
};

const SDGDetailPage = () => {
  const { sdgNumber } = useParams();
  const [sdg, setSdg] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchSDGData = async () => {
      setIsLoading(true);
      try {
        // 檢查是否使用範例數據
        if (sdgNumber === '1') { // 假設 sdgNumber 為 1 時，使用範例數據
          setSdg(sdgExample);
        } else {
          const response = await fetch(`http://localhost:3000/api/sdg/${sdgNumber}`);
          if (!response.ok) throw new Error('Failed to fetch SDG data');
          const data = await response.json();
          setSdg(data);
        }
      } catch (error) {
        console.error("Error fetching SDG data:", error);
        setError(error.message || '獲取數據時發生錯誤');
      } finally {
        setIsLoading(false);
      }
    };

    fetchSDGData();
  }, [sdgNumber]);

  useEffect(() => {
    if (!sdg && !isLoading && !error) {
      // 如果sdg數據為空且沒有加載和錯誤信息，導航到聯合國SDG網頁
      window.location.href = `https://sdgs.un.org/goals/goal${sdgNumber}`;
    }
  }, [sdg, isLoading, error, sdgNumber]);

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gray-900 text-white">
        <Navbar />
        <div className="container mx-auto px-4 py-8">
          <LoadingSpinner />
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-900 text-white">
        <Navbar />
        <div className="container mx-auto px-4 py-8">
          <ErrorMessage message={error} />
        </div>
      </div>
    );
  }

  if (!sdg) {
    return null; // 確保在sdg為空時不會渲染任何內容
  }

  return (
    <div className="min-h-screen bg-gray-900 text-white flex flex-col">
      <Navbar />
      <div className="flex-grow container mx-auto px-4 py-8">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="bg-gray-800 rounded-xl shadow-lg overflow-hidden"
        >
          <div className="bg-gradient-to-r from-blue-600 to-green-600 p-6">
            <h1 className="text-3xl font-bold flex items-center">
              <Globe className="mr-3" size={36} />
              SDG {sdg.number}: {sdg.title}
            </h1>
          </div>
          
          <div className="p-6 space-y-6">
            <div className="bg-gray-700 p-4 rounded-lg">
              <h2 className="text-xl font-semibold mb-2 flex items-center">
                <Target className="mr-2" /> 目標描述
              </h2>
              <p>{sdg.description}</p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="bg-gray-700 p-4 rounded-lg">
                <h2 className="text-xl font-semibold mb-2 flex items-center">
                  <BarChart className="mr-2" /> 關鍵指標
                </h2>
                <ul className="list-disc list-inside">
                  {sdg.keyIndicators.map((indicator, index) => (
                    <li key={index}>{indicator}</li>
                  ))}
                </ul>
              </div>
              <div className="bg-gray-700 p-4 rounded-lg">
                <h2 className="text-xl font-semibold mb-2 flex items-center">
                  <Users className="mr-2" /> 目標群體
                </h2>
                <p>{sdg.targetGroups}</p>
              </div>
            </div>

            <div className="bg-gray-700 p-4 rounded-lg">
              <h2 className="text-xl font-semibold mb-2 flex items-center">
                <Lightbulb className="mr-2" /> 行動建議
              </h2>
              <ul className="space-y-2">
                {sdg.actionItems.map((item, index) => (
                  <li key={index} className="flex items-start">
                    <span className="bg-green-500 rounded-full p-1 mr-2 mt-1">
                      <Lightbulb size={16} />
                    </span>
                    {item}
                  </li>
                ))}
              </ul>
            </div>

            <div className="bg-gray-700 p-4 rounded-lg">
              <h2 className="text-xl font-semibold mb-2 flex items-center">
                <BookOpen className="mr-2" /> 相關條文
              </h2>
              {sdg.relatedArticles.map((article, index) => (
                <Link 
                  key={index} 
                  to={`/treaty/${sdg.number}#${article.id}`}
                  className="block mb-2 hover:bg-gray-600 p-2 rounded transition duration-300"
                >
                  <h3 className="font-semibold">{article.title}</h3>
                  <p className="text-sm text-gray-300">{article.content.substring(0, 100)}...</p>
                </Link>
              ))}
            </div>
          </div>
        </motion.div>
      </div>
    </div>
  );
};

export default SDGDetailPage;
