import React, { useState, useCallback } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { MessageCircle, ChevronRight, ChevronLeft, Plus, X } from 'lucide-react';

const GuidedContextAnalysis = ({ assessment, updateAssessment }) => {
  const [currentStep, setCurrentStep] = useState(0);
  const [showAI, setShowAI] = useState(false);
  const [newItem, setNewItem] = useState('');

  const steps = [
    { title: '項目目標', field: 'projectGoal' },
    { title: '項目範圍', field: 'projectScope' },
    { title: '主要挑戰', field: 'mainChallenges' },
    { title: '利益相關者', field: 'stakeholders' },
    { title: '弱勢群體', field: 'vulnerableGroups' },
  ];

  const handleInputChange = useCallback((value) => {
    const field = steps[currentStep].field;
    updateAssessment('context', { ...assessment.context, [field]: value });
  }, [assessment.context, currentStep, steps, updateAssessment]);

  const handleAddItem = useCallback(() => {
    if (newItem.trim()) {
      const field = steps[currentStep].field;
      updateAssessment('context', {
        ...assessment.context,
        [field]: [...(assessment.context[field] || []), newItem.trim()]
      });
      setNewItem('');
    }
  }, [assessment.context, currentStep, newItem, steps, updateAssessment]);

  const handleRemoveItem = useCallback((index) => {
    const field = steps[currentStep].field;
    const updatedItems = assessment.context[field].filter((_, i) => i !== index);
    updateAssessment('context', { ...assessment.context, [field]: updatedItems });
  }, [assessment.context, currentStep, steps, updateAssessment]);

  const handleAISuggest = useCallback((suggestion) => {
    const field = steps[currentStep].field;
    updateAssessment('context', { ...assessment.context, [field]: suggestion });
    setShowAI(false);
  }, [assessment.context, currentStep, steps, updateAssessment]);

  const isLastStep = currentStep === steps.length - 1;
  const isFirstStep = currentStep === 0;

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="space-y-6"
    >
      <h2 className="text-3xl font-bold mb-6 text-gray-100">背景分析</h2>

      <div className="bg-gray-800 bg-opacity-50 p-6 rounded-lg shadow-lg backdrop-blur-sm">
        <h3 className="text-xl font-semibold mb-4 text-gray-100">{steps[currentStep].title}</h3>
        
        {['stakeholders', 'vulnerableGroups'].includes(steps[currentStep].field) ? (
          <div className="space-y-4">
            <div className="flex flex-wrap gap-2 mb-2">
              {(assessment.context[steps[currentStep].field] || []).map((item, index) => (
                <span key={index} className="px-3 py-1 bg-blue-500 bg-opacity-50 text-white rounded-full text-sm flex items-center">
                  {item}
                  <button onClick={() => handleRemoveItem(index)} className="ml-2 text-white hover:text-red-200 transition duration-200">
                    <X size={14} />
                  </button>
                </span>
              ))}
            </div>
            <div className="flex">
              <input
                type="text"
                value={newItem}
                onChange={(e) => setNewItem(e.target.value)}
                className="flex-grow p-2 bg-gray-700 border border-gray-600 rounded-l-lg text-white focus:ring-2 focus:ring-blue-500 focus:border-transparent transition duration-200"
                placeholder={`添加新的${steps[currentStep].title}`}
              />
              <button
                onClick={handleAddItem}
                className="px-4 bg-blue-500 text-white rounded-r-lg hover:bg-blue-600 transition duration-200 flex items-center justify-center"
              >
                <Plus size={20} />
              </button>
            </div>
          </div>
        ) : (
          <div className="relative">
            <textarea
              value={assessment.context[steps[currentStep].field] || ''}
              onChange={(e) => handleInputChange(e.target.value)}
              rows={4}
              className="w-full p-3 bg-gray-700 bg-opacity-50 border border-gray-600 rounded-lg text-white resize-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition duration-200"
              placeholder={`請描述${steps[currentStep].title}...`}
            />
            <button
              onClick={() => setShowAI(true)}
              className="absolute right-2 top-2 text-blue-400 hover:text-blue-300 transition duration-200"
              title="獲取 AI 建議"
            >
              <MessageCircle size={20} />
            </button>
          </div>
        )}

        <div className="flex justify-between mt-6">
          <button
            onClick={() => setCurrentStep(prev => Math.max(0, prev - 1))}
            className={`px-4 py-2 flex items-center ${
              isFirstStep ? 'text-gray-500 cursor-not-allowed' : 'text-blue-400 hover:text-blue-300'
            } transition duration-200`}
            disabled={isFirstStep}
          >
            <ChevronLeft size={20} className="mr-2" /> 上一步
          </button>
          <button
            onClick={() => setCurrentStep(prev => Math.min(steps.length - 1, prev + 1))}
            className={`px-4 py-2 flex items-center ${
              isLastStep ? 'text-gray-500 cursor-not-allowed' : 'text-blue-400 hover:text-blue-300'
            } transition duration-200`}
            disabled={isLastStep}
          >
            下一步 <ChevronRight size={20} className="ml-2" />
          </button>
        </div>
      </div>

      <AnimatePresence>
        {showAI && (
          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.95 }}
            transition={{ duration: 0.2 }}
            className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 backdrop-blur-sm"
          >
            <div className="bg-gray-800 bg-opacity-90 p-6 rounded-lg shadow-xl max-w-md w-full">
              <h3 className="text-xl font-bold mb-4 text-white">AI 建議</h3>
              <p className="text-gray-300 mb-4">AI 正在生成關於{steps[currentStep].title}的建議...</p>
              <div className="flex justify-end space-x-2">
                <button
                  onClick={() => handleAISuggest(`AI 生成的${steps[currentStep].title}建議內容`)}
                  className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition duration-200"
                >
                  採用建議
                </button>
                <button
                  onClick={() => setShowAI(false)}
                  className="px-4 py-2 bg-gray-600 text-white rounded hover:bg-gray-700 transition duration-200"
                >
                  關閉
                </button>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </motion.div>
  );
};

export default GuidedContextAnalysis;